/* eslint-disable functional/immutable-data */
/* eslint-disable no-null/no-null */
import { findObjectEntries, findObjectEntry, getObjectPath } from '../../utils';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Inject,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { IFinderNode } from '../../types/interfaces/finder.interface';
import { CdkDragDrop } from '@angular/cdk/drag-drop';

@UntilDestroy()
@Component({
    selector: 'ads-finder',
    templateUrl: './finder.component.html',
    styleUrls: ['./finder.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFinderComponent implements OnChanges, AfterViewInit, OnInit {
    @Input() css!: string;
    @Input() description!: string;
    @Input() deselect!: string | undefined;
    @Input() select!: string | undefined;
    @Input() list!: Array<IFinderNode>;
    @Input() isMultiple = true;
    @Output() readonly finderChangeEmitter: EventEmitter<IFinderNode> = new EventEmitter();
    @Output() readonly reOrderEmitter: EventEmitter<CdkDragDrop<Array<IFinderNode>>> =
        new EventEmitter();

    search = '';
    nodePath: Array<{ id: string; name: string }> = [];
    searchresults: Array<string> = [];

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        private readonly cdr: ChangeDetectorRef,
    ) {}

    private selectFirstNodeWhenNeeded(list: Array<IFinderNode>) {
        if (list.length === 1) {
            this.selectNode(list[0]);
            this.cdr.detectChanges();
        }
    }

    private clearSearchData() {
        this.nodePath = [];
        this.selectFirstNodeWhenNeeded(this.list);
    }

    ngOnInit(): void {
        this.selectFirstNodeWhenNeeded(this.list);
    }

    // ---

    levelUp(): void {
        this.scrollIntoViewHorizontal(this.nodePath[this.nodePath.length - 2]?.id ?? '0');
        this.nodePath.pop();
    }

    // ---

    searchNodes(value: string): void {
        this.searchresults = [];

        if (value !== '') {
            const results = findObjectEntries(this.list, 'name', value);
            if (results.length > 0) {
                this.nodePath = getObjectPath(this.list, results[0].id);
                results.forEach((result: Record<string, string>) =>
                    getObjectPath(this.list, result['id']).forEach((path: Record<string, string>) =>
                        this.searchresults.push(path['id']),
                    ),
                );
            } else {
                this.clearSearchData();
            }
        } else {
            this.clearSearchData();
        }
    }

    // ---

    searchNodeById(id: string): void {
        this.searchresults = [];

        if (id !== '') {
            const results = findObjectEntries(this.list, 'id', id);
            if (results.length > 0) {
                this.nodePath = getObjectPath(this.list, results[0].id);
                results.forEach((result: Record<string, string>) =>
                    getObjectPath(this.list, result['id']).forEach((path: Record<string, string>) =>
                        this.searchresults.push(path['id']),
                    ),
                );
            }
        }
    }

    // ---

    selectNode(node: IFinderNode): void {
        if (!this.nodeInPath(node.id.toString())) {
            this.nodePath = getObjectPath(this.list, node.id);
            if (node.children) {
                this.scrollIntoViewHorizontal(node.id);
                this.selectFirstNodeWhenNeeded(node.children);
            }
        } else {
            this.nodePath = getObjectPath(this.list, node.id);
            this.nodePath.pop();
        }
    }

    // ---

    toggleOption(node: IFinderNode): void {
        this.finderChangeEmitter.emit(node);
    }

    // ---

    nodeInPath(id: string): boolean {
        return this.nodePath.find(item => item.id === id) ? true : false;
    }

    // ---

    scrollIntoViewHorizontal(id: string): void {
        const element: HTMLElement | null = document.getElementById(`finder-list-${id}`);
        if (element) {
            setTimeout(() => {
                element.scrollIntoView({
                    behavior: 'auto',
                    block: 'nearest',
                    inline: 'start',
                });
            }, 100);
        }
    }

    // ---

    trackByIndex(index: number): number {
        return index;
    }

    // ---

    ngOnChanges(changes: SimpleChanges): void {
        const { deselect, list, select } = changes;
        if (list) {
            this.list = list.currentValue;
            this.cdr.detectChanges();
        }

        if (deselect && deselect.currentValue) {
            const result: IFinderNode = findObjectEntry(this.list, 'id', deselect.currentValue);
            if (result) {
                result.selected = false;
                this.toggleOption(result);
            }
        }

        if (select && select.currentValue) {
            this.searchNodeById(select.currentValue);
        }
    }

    // ---

    ngAfterViewInit() {
        fromEvent(this.windowRef, 'resize')
            .pipe(untilDestroyed(this))
            .subscribe(() => {
                if (this.nodePath[this.nodePath.length - 1]) {
                    this.scrollIntoViewHorizontal(this.nodePath[this.nodePath.length - 1].id);
                }
            });
    }

    // Returns the number of direct children that can lead to an actual item, or 1 if it is an item itself
    getVisibleChildCount(node: IFinderNode): number {
        // if this node is the lowest level, return 1
        if (!node.children) {
            return 1;
        }

        return node.children?.reduce((accumulator, currentValue) => {
            return accumulator + (this.getVisibleChildCount(currentValue) ? 1 : 0);
        }, 0);
    }
}
