/* eslint-disable functional/immutable-data */
import { DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    Inject,
    Input,
    ViewChild,
} from '@angular/core';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ADSKeyEvents } from '../../types';
import { CollisionDetectionService, randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-context-menu',
    templateUrl: './context-menu.component.html',
    styleUrls: ['./context-menu.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSContextMenuComponent implements AfterViewInit {
    @ViewChild('contextMenu') contextMenu!: ElementRef;
    @Input() css!: string;
    @Input() size: 'small' | 'regular' = 'regular';

    readonly id = `ads-context-menu-${randomId()}`;
    private buttonElement!: HTMLButtonElement;
    private menuElement!: HTMLUListElement;
    private menuElementItems!: Array<HTMLLIElement>;
    private selectedMenuItem = 0;

    visible = false;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly cdr: ChangeDetectorRef,
        private readonly collisionDetection: CollisionDetectionService,
    ) {}

    private toggle(): void {
        this.visible = !this.visible;
        this.buttonElement.setAttribute('aria-expanded', this.visible.toString());
        this.cdr.detectChanges();

        if (this.visible) {
            this.collisionDetection.detect(this.contextMenu.nativeElement, this.menuElement);
        }
    }

    private first(): void {
        this.selectedMenuItem = 0;
        this.selectMenuItem();
    }

    private last(): void {
        this.selectedMenuItem = this.menuElementItems.length - 1;
        this.selectMenuItem();
    }

    private next(): void {
        this.selectedMenuItem =
            this.selectedMenuItem + 1 > this.menuElementItems.length - 1
                ? 0
                : this.selectedMenuItem + 1;
        this.selectMenuItem();
    }

    private previous(): void {
        this.selectedMenuItem =
            this.selectedMenuItem - 1 < 0
                ? this.menuElementItems.length - 1
                : this.selectedMenuItem - 1;
        this.selectMenuItem();
    }

    private selectMenuItem(): void {
        const button: HTMLButtonElement | null =
            this.menuElementItems[this.selectedMenuItem].querySelector('.ads-button');
        const checkbox: HTMLInputElement | null =
            this.menuElementItems[this.selectedMenuItem].querySelector('.ads-form-checkbox');
        button?.focus();
        checkbox?.focus();
    }

    ngAfterViewInit() {
        this.buttonElement = this.contextMenu.nativeElement.querySelector('.ads-button');
        this.menuElement = this.contextMenu.nativeElement.querySelector('ul');
        this.menuElementItems = Array.from(this.menuElement.querySelectorAll('li')).filter(
            (item: HTMLLIElement) =>
                item.querySelector('.ads-button') ?? item.querySelector('.ads-form-checkbox'),
        );

        if (this.buttonElement) {
            this.buttonElement.setAttribute('aria-controls', this.id);
            this.buttonElement.setAttribute('aria-expanded', 'false');
            this.buttonElement.setAttribute('aria-haspopup', 'true');

            this.menuElement.setAttribute('id', this.id);
            this.menuElement.setAttribute('role', 'list');
            this.menuElement.setAttribute('tabindex', '-1');

            fromEvent<MouseEvent>(this.buttonElement, 'click')
                .pipe(untilDestroyed(this))
                .subscribe(() => {
                    this.toggle();
                    if (this.visible) {
                        this.first();
                    } else {
                        this.buttonElement.focus();
                    }
                });

            fromEvent<KeyboardEvent>(this.buttonElement, 'keydown')
                .pipe(untilDestroyed(this))
                .subscribe((event: KeyboardEvent) => {
                    if (
                        event.key === ADSKeyEvents.KEY_SPACEBAR ||
                        event.key === ADSKeyEvents.KEY_ARROW_DOWN ||
                        event.key === ADSKeyEvents.KEY_ENTER
                    ) {
                        event.preventDefault();
                        this.toggle();
                        this.first();
                    }
                    if (event.key === ADSKeyEvents.KEY_ARROW_UP) {
                        event.preventDefault();
                        this.toggle();
                        this.last();
                    }
                    if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                        if (this.visible) {
                            this.toggle();
                            this.buttonElement.focus();
                        }
                    }
                });

            fromEvent<KeyboardEvent>(this.menuElement, 'keydown')
                .pipe(
                    untilDestroyed(this),
                    filter(() => this.visible),
                )
                .subscribe((event: KeyboardEvent) => {
                    if (event.key === ADSKeyEvents.KEY_TAB) {
                        this.toggle();
                    }
                    if (event.key === ADSKeyEvents.KEY_ARROW_DOWN) {
                        event.preventDefault();
                        this.next();
                    }
                    if (event.key === ADSKeyEvents.KEY_ARROW_UP) {
                        event.preventDefault();
                        this.previous();
                    }
                    if (event.key === ADSKeyEvents.KEY_HOME) {
                        event.preventDefault();
                        this.first();
                    }
                    if (event.key === ADSKeyEvents.KEY_END) {
                        event.preventDefault();
                        this.last();
                    }
                    if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                        this.toggle();
                        this.buttonElement?.focus();
                    }
                });

            fromEvent<MouseEvent>(this.menuElement, 'click')
                .pipe(
                    untilDestroyed(this),
                    filter(() => this.visible),
                )
                .subscribe((event: MouseEvent) => {
                    const target = event.target as Element;
                    const li: HTMLLIElement | null = target?.closest('li');
                    // only close if its a button
                    if (li?.firstChild?.nodeName === 'ADS-BUTTON') {
                        this.toggle();
                        this.buttonElement.focus();
                    }
                });

            fromEvent<MouseEvent>(this.document, 'click')
                .pipe(
                    untilDestroyed(this),
                    filter(() => this.visible),
                )
                .subscribe((event: MouseEvent) => {
                    const target = event.target as Element;
                    const parent = target.closest('.ads-context-menu');
                    if (!parent || parent.id !== this.id) {
                        this.toggle();
                    }
                });

            fromEvent(this.windowRef, 'resize')
                .pipe(
                    untilDestroyed(this),
                    filter(() => this.visible),
                )
                .subscribe(() =>
                    this.collisionDetection.detect(
                        this.contextMenu.nativeElement,
                        this.menuElement,
                    ),
                );
        }
    }
}
