// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const translateDatatableOrderingTranslation: any = {
    ascending: $localize`:@@datatableOrderingAscending:ascending`,
    descending: $localize`:@@datatableOrderingDescending:descending`,
};

export function translateDatatableOrdering(ordering: string): string {
    return translateDatatableOrderingTranslation[ordering]
        ? translateDatatableOrderingTranslation[ordering]
        : `Missing translation ${ordering}`;
}
