/* eslint-disable functional/immutable-data */
/* eslint-disable @typescript-eslint/no-explicit-any */

export function findObjectEntries(object: any, key: any, value: any): any {
    const entries: Array<any> = [];

    const find = (obj: any) => {
        for (const child of obj) {
            for (const [objectKey, objectValue] of Object.entries(child) as any) {
                if (
                    objectKey === key &&
                    key !== 'id' &&
                    objectValue.toString().toLowerCase().indexOf(value.toString().toLowerCase()) >
                        -1
                ) {
                    entries.push({ name: child.name, id: child.id });
                }
                if (
                    objectKey === key &&
                    key === 'id' &&
                    objectValue.toString().toLowerCase() === value.toString().toLowerCase()
                ) {
                    entries.push({ name: child.name, id: child.id });
                }
                if (Array.isArray(objectValue)) {
                    find(objectValue);
                }
            }
        }
    };

    find(object);

    return entries;
}
