/* eslint-disable functional/immutable-data */
/* eslint-disable @typescript-eslint/no-explicit-any */

export function findObjectEntry(object: any, key: any, value: any) {
    for (const child of object) {
        for (const [objectKey, objectValue] of Object.entries(child) as any) {
            if (
                objectKey === key &&
                key !== 'id' &&
                objectValue.toString().toLowerCase().indexOf(value.toString().toLowerCase()) > -1
            ) {
                return child;
            }
            if (
                objectKey === key &&
                key === 'id' &&
                objectValue.toString().toLowerCase() === value.toString().toLowerCase()
            ) {
                return child;
            }
            if (Array.isArray(objectValue)) {
                const newObject: any = findObjectEntry(objectValue, key, value);
                if (newObject) {
                    return newObject;
                }
            }
        }
    }
}
