/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    Input,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { delay, of } from 'rxjs';
import { IADSDecoratorsColorsFunctional, IADSDecoratorsTypesNotifications } from '../../types';

@UntilDestroy()
@Component({
    selector: 'ads-notification',
    templateUrl: './notification.component.html',
    styleUrls: ['./notification.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSNotificationComponent implements AfterViewInit {
    @Input() css!: string;
    @Input() description!: string;
    @Input() label!: string;
    @Input() status: IADSDecoratorsColorsFunctional = 'info';
    @Input() type: IADSDecoratorsTypesNotifications = 'default';
    @Input() automaticClose = false;

    visible = true;
    destroyed = false;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    hide() {
        this.visible = false;
        this.cdr.detectChanges();
        of(true)
            .pipe(delay(1000), untilDestroyed(this))
            .subscribe(() => {
                this.destroyed = true;
                this.cdr.detectChanges();
            });
    }

    ngAfterViewInit() {
        of(true)
            .pipe(delay(4000), untilDestroyed(this))
            .subscribe(() => (this.visible && this.automaticClose ? this.hide() : false));
    }
}
