/* eslint-disable functional/immutable-data */
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
} from '@angular/core';
import { IADSLanguage } from '../../types';
@Component({
    selector: 'ads-language-selector',
    templateUrl: './language-selector.component.html',
    styleUrls: ['./language-selector.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSLanguageSelectorComponent implements OnChanges {
    @Input() currentLanguage = '';
    @Input() availableLanguages!: ReadonlyArray<IADSLanguage>;
    @Input() css!: string;
    @Output() readonly languageChangeEmitter: EventEmitter<string> = new EventEmitter();

    currentLanguageObject: IADSLanguage | undefined;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    switchLanguage(language: string) {
        this.currentLanguage = language;
        this.updateCurrentLanguageObject();
        this.languageChangeEmitter.emit(language);
    }

    trackByCode(index: number, el: IADSLanguage) {
        return el.code;
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { currentLanguage, availableLanguages } = changes;

        if (currentLanguage) {
            this.currentLanguage = currentLanguage.currentValue;
        }

        if (availableLanguages) {
            this.availableLanguages = availableLanguages.currentValue;
        }

        if (currentLanguage || availableLanguages) {
            this.updateCurrentLanguageObject();
            this.cdr.detectChanges();
        }
    }

    private updateCurrentLanguageObject() {
        this.currentLanguageObject = this.availableLanguages.find(
            language => language.code === this.currentLanguage,
        );
    }
}
