/* eslint-disable functional/immutable-data */
/* eslint-disable no-null/no-null */
/* eslint-disable @angular-eslint/use-component-view-encapsulation */
import { DatePipe } from '@angular/common';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewEncapsulation,
} from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder } from '@angular/forms';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { UntilDestroy } from '@ngneat/until-destroy';
import { translateDatatableOrdering } from '../../translations/data-table.ordering.translations';
import {
    ITable,
    ITableColumn,
    ITableData,
    ITableDataRow,
    ITableFilters,
    ITableRow,
    ITableSorting,
    ITableTemplate,
} from '../../types';
import { flattenObject, randomId } from '../../utils';
import { PaginationEvent } from '../paginator/paginator.component';

export type RowActionClickEvent = {
    action: string;
    row: ITableDataRow;
    clickEvent: MouseEvent;
};

export type RowValueChangeEvent = {
    row: ITableDataRow;
    column: ITableColumn;
    value: string;
};

@UntilDestroy()
@Component({
    selector: 'ads-data-table',
    templateUrl: './data-table.component.html',
    styleUrls: ['./data-table.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
})
export class ADSDataTableComponent implements OnChanges {
    @Input() data: ITable | undefined | null = undefined;
    @Input() template!: ITableTemplate;
    @Input() currentLanguage = 'en-US';
    @Input() loadingLabel = 'Loading...';
    @Input() errorLabel = 'An error occurred while loading data.';
    @Input() hasError = false;
    @Input() hasFixedLayout = false;

    @Output() readonly clickEmitter = new EventEmitter<RowActionClickEvent>();
    @Output() readonly rowValueChange = new EventEmitter<RowValueChangeEvent>();

    @Output() readonly searchQuery: EventEmitter<{
        searchTerm: string;
    }> = new EventEmitter();

    private dataOrigin: ITable | undefined = undefined;
    private datePipe: DatePipe;
    emptyValueDescription: string = $localize`:@@datatableFilterEmptyValue: <empty>`;

    readonly tableForm = this.formBuilder.group({
        columns: this.formBuilder.array([]),
        filters: this.formBuilder.array([]),
        search: [''],
    });
    readonly columns = this.tableForm.get('columns') as UntypedFormArray;
    readonly filters = this.tableForm.get('filters') as UntypedFormArray;
    readonly search = this.tableForm.get('search');

    displaySelectedOnly = false;
    filtersChanged = false;
    sorting: ITableSorting = { direction: '' };
    tableTemplate!: ITableTemplate;
    id = randomId();
    tableData!: ITableData;
    tableDataSlice!: ITableData;
    tableDataSliceStart!: number;
    tableDataSliceEnd!: number;
    pageIndex = 1;
    pageSize = 0;

    constructor(
        private readonly formBuilder: UntypedFormBuilder,
        private readonly cdr: ChangeDetectorRef,
        private readonly sanitizer: DomSanitizer,
    ) {
        this.datePipe = new DatePipe(this.currentLanguage);
    }

    // reduce the raw data input to an array of sorted, formatted data that we are interested in
    updateTableData(): void {
        if (this.tableTemplate) {
            const searchString = this.search?.value.toLowerCase() ?? '';

            if (this.dataOrigin) {
                this.tableData = this.dataOrigin.reduce(
                    (table: Array<ITableDataRow>, rowOrigin: ITableRow) => {
                        const row = { ...rowOrigin };
                        const activeFilters = this.filters.value.reduce(
                            (
                                accumulator: Array<{ key: string; labels: Array<string> }>,
                                currentValue: ITableFilters,
                            ) => {
                                if (!currentValue.header && !currentValue.selected) {
                                    const target = accumulator.find(
                                        i => i.key === currentValue.key,
                                    );
                                    if (target) {
                                        target.labels = [...target.labels, currentValue.label];

                                        return accumulator;
                                    }

                                    return [
                                        ...accumulator,
                                        { key: currentValue.key, labels: [currentValue.label] },
                                    ];
                                }

                                return accumulator;
                            },
                            [],
                        );

                        let searchResult = searchString === '';
                        this.tableTemplate.columns.forEach(column => {
                            const value = row[column.dataKey]?.toString() ?? '';
                            if (!searchResult) {
                                searchResult = value.toLowerCase().indexOf(searchString) > -1;
                            }
                        });

                        let filterResult = false;

                        activeFilters.forEach(
                            (activeFilter: { key: string; labels: Array<string> }) => {
                                if (typeof row[activeFilter.key] === 'object') {
                                    const filteredArray: Array<string> = (
                                        row[activeFilter.key] as Array<string>
                                    )?.filter((value: string) => {
                                        return activeFilter.labels.includes(value);
                                    });
                                    if (
                                        filteredArray.length ===
                                        (row[activeFilter.key] as Array<string>).length
                                    ) {
                                        filterResult = true;
                                    }
                                } else {
                                    const value = row[activeFilter.key]?.toString() ?? '';
                                    if (activeFilter.labels.includes(value)) {
                                        filterResult = true;
                                    }
                                }
                            },
                        );

                        // parse arrays to string for showing in table
                        this.tableTemplate.columns.forEach(column => {
                            if (column.type === 'array') {
                                row[column.dataKey] = (row[column.dataKey] as Array<string>)
                                    .sort()
                                    .join(', ');
                            }
                        });

                        if (searchResult && !filterResult) {
                            table.push(row as ITableDataRow);
                        }

                        return table;
                    },
                    [],
                );
            }

            if (this.sorting.direction !== '') {
                this.tableData = this.sortTableData();
            }

            this.updateTableDataSlice();
        }
    }

    private updateTableDataSlice(): void {
        if (this.tableData) {
            this.tableDataSlice = this.tableData.map(row => {
                // format dates
                const dateColumns = this.tableTemplate.columns.filter(c => c.type === 'date');
                for (const column of dateColumns) {
                    try {
                        row[column.dataKey] =
                            this.datePipe.transform(
                                row[column.dataKey],
                                column.dateFormat ?? 'medium',
                            ) ?? '';
                    } catch (e) {
                        // display unformatted input if date pipe fails
                        row[column.dataKey];
                    }
                }

                return row;
            });
            if (this.tableTemplate.pagination) {
                // Take part of the table data when we have pagination for this data table enabled
                this.tableDataSliceStart = (this.pageIndex - 1) * this.pageSize;
                this.tableDataSlice = this.tableData.slice(
                    this.tableDataSliceStart,
                    this.tableDataSliceStart + this.pageSize,
                );
                this.tableDataSliceEnd = this.tableDataSliceStart + this.tableDataSlice.length;
            } else {
                this.tableDataSlice = this.tableData;
            }
            this.cdr.detectChanges();
        }
    }

    // --- pagination
    pageChanged(paginationEvent: PaginationEvent) {
        this.pageIndex = paginationEvent.pageIndex;
        this.pageSize = paginationEvent.pageSize;
        this.updateTableDataSlice();
    }

    getNumberOfResultsDisplayed(): string {
        return this.tableDataSlice ? this.tableDataSlice.length.toString() : '0';
    }

    // --- sorting ---
    sortByColumn(column: ITableColumn) {
        if (this.sorting.column?.dataKey !== column.dataKey) {
            this.sorting.direction = 'ascending';
            this.sorting.column = column;
        } else {
            this.sorting.direction =
                this.sorting.direction === 'ascending' ? 'descending' : 'ascending';
        }
        this.sortTableData();
        this.updateTableDataSlice();
    }

    sortTableData(): ITableData {
        if (this.sorting.direction === 'ascending') {
            return this.tableData.sort((a, b) => this.sort(a, b));
        }

        return this.tableData.sort((a, b) => this.sort(b, a));
    }

    private sort(rowA: ITableDataRow, rowB: ITableDataRow) {
        if (!this.sorting.column?.dataKey) {
            return 0;
        }

        let valueA: number | string | boolean = rowA[this.sorting.column.dataKey];
        let valueB: number | string | boolean = rowB[this.sorting.column.dataKey];

        if (this.sorting.column.type === 'date') {
            valueA = new Date(valueA).getTime();
            valueB = new Date(valueB).getTime();

            return valueA > valueB ? 1 : -1;
        }

        if (this.sorting.column.type === 'string' || this.sorting.column.type === 'image') {
            return valueA.localeCompare(valueB);
        }

        return valueA > valueB ? 1 : -1;
    }

    translateSortingDirection(sortingDirection: string): string {
        return translateDatatableOrdering(sortingDirection);
    }

    getSortClass(column: ITableColumn): string {
        if (column.dataKey === this.sorting.column?.dataKey) {
            return this.sorting.direction === 'ascending' ? 'sort-ascending' : 'sort-descending';
        }

        return 'sort-hidden';
    }

    emitClick($event: MouseEvent, action: string, row: ITableDataRow) {
        $event.stopPropagation();
        this.clickEmitter.emit({ action, row, clickEvent: $event });
    }

    emitRowValueChange(column: ITableColumn, row: ITableDataRow, value: string) {
        this.rowValueChange.emit({
            column,
            row,
            value,
        });
    }

    formSubscriptions(): void {
        this.filters.valueChanges.subscribe(() => {
            this.updateFiltersChanged();
            this.updateTableData();
        });

        this.search?.valueChanges.subscribe(() => this.updateTableData());
    }

    trackByIndex(index: number): number {
        return index;
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { data, currentLanguage, template } = changes;
        if (currentLanguage && currentLanguage.currentValue) {
            this.datePipe = new DatePipe(this.currentLanguage);
        }

        if (data && data.currentValue) {
            this.dataOrigin = JSON.parse(JSON.stringify(data.currentValue));
        }

        if (template && template.currentValue) {
            this.tableTemplate = template.currentValue;
            this.sorting.column = this.tableTemplate.columns.find(
                c => c.dataKey === this.tableTemplate.sort?.dataKey,
            );
            this.sorting.direction = this.tableTemplate.sort?.direction ?? 'descending';
        }

        // When either the template or the data is changed perform an update
        if ((template && template.currentValue) || (data && data.currentValue)) {
            this.loadFilterOptions();
            if (!this.pageSize && this.tableTemplate.pagination) {
                this.pageSize = this.tableTemplate.pagination.pageSize;
            }

            if (data.currentValue) {
                this.updateTableData();
            }
            this.formSubscriptions();
        }
    }

    getSanitizedHtml(html: string): SafeHtml {
        return this.sanitizer.bypassSecurityTrustHtml(html);
    }

    loadFilterOptions(useCache = true): void {
        const filterCache = useCache ? [...this.filters.value] : [];
        this.filters.clear();

        if (this.tableTemplate.filters) {
            this.tableTemplate.filters.forEach(filter => {
                const targetColumn = this.tableTemplate.columns.find(c => c.dataKey === filter.key);
                if (!targetColumn) {
                    return;
                }
                this.filters.push(
                    this.formBuilder.group({
                        key: 'header',
                        label: targetColumn.title,
                        selected: true,
                        header: true,
                    }),
                );

                const filterValues = new Set(
                    Object.values(flattenObject(this.dataOrigin?.map(row => row[filter.key]))).sort(
                        (a, b) => (!a || !b ? 0 : a.toString()?.localeCompare(b.toString())),
                    ),
                );

                filterValues.forEach(entry => {
                    const cached = filterCache.find(
                        f => f.label === entry?.toString() && f.key === filter.key,
                    );

                    filter.selected = filter.selected ?? [];
                    const selected = !filter.selected.includes(entry?.toString() ?? '');

                    return this.filters.push(
                        this.formBuilder.group({
                            key: filter.key,
                            label: entry?.toString(),
                            selected: cached ? cached.selected : selected,
                            header: false,
                        }),
                    );
                });
            });
        }
    }

    deselectSection(label: string) {
        const key = this.template.columns.find(col => col.title === label)?.dataKey;
        const targetFilters = this.filters.value.filter(
            (f: { key: string | undefined }) => f.key === key,
        );

        targetFilters.forEach((filter: { selected: boolean }) => {
            filter.selected = false;
        });
        this.updateFiltersChanged();
        this.updateTableData();
    }

    updateFiltersChanged(): void {
        this.filtersChanged = false;
        this.filters.value.forEach(
            (formFilter: { key: string; label: string; selected: Array<string> | undefined }) => {
                // find corresponding filter from template
                const match = this.tableTemplate.filters?.find(
                    filter => filter.key === formFilter.key,
                );

                if (!match) {
                    return;
                }
                // compare enabled state of current filter with temaplate
                if (
                    (match.selected?.includes(formFilter.label) && formFilter.selected) ||
                    (!match.selected?.includes(formFilter.label) && !formFilter.selected)
                ) {
                    this.filtersChanged = true;
                }
            },
        );
    }
}
