/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    Inject,
    Input,
    ViewChild,
} from '@angular/core';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import {
    IADSDecoratorsColorsBasic,
    IADSDecoratorsColorsFunctional,
    IADSDecoratorsColorsMain,
    IADSDecoratorsSizesIcon,
} from '../../types';
import { CollisionDetectionService, randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-tooltip',
    templateUrl: './tooltip.component.html',
    styleUrls: ['./tooltip.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSTooltipComponent implements AfterViewInit {
    @ViewChild('tooltip') tooltip!: ElementRef;
    @Input() css!: string;
    @Input() color:
        | IADSDecoratorsColorsMain
        | IADSDecoratorsColorsFunctional
        | IADSDecoratorsColorsBasic = 'primary';
    @Input() icon!: string;
    @Input() label!: string;
    @Input() size: IADSDecoratorsSizesIcon = 'regular';
    readonly id: string = `al-tooltip-${randomId()}`;
    private buttonElement!: HTMLButtonElement;
    private tipElement!: HTMLSpanElement;
    private wrapperElement!: HTMLSpanElement;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        private readonly collisionDetection: CollisionDetectionService,
    ) {}

    private show(): void {
        this.buttonElement.setAttribute('aria-expanded', 'true');
        this.collisionDetection.detect(this.wrapperElement, this.tipElement);
    }

    private hide(): void {
        this.buttonElement.setAttribute('aria-expanded', 'false');
    }

    tooltipMouseOver() {
        this.show();
    }

    tooltipMouseOut() {
        this.hide();
    }

    tooltipFocus() {
        this.wrapperElement.classList.add('has-focus');
        this.show();
    }

    tooltipBlur() {
        this.wrapperElement.classList.remove('has-focus');
        this.hide();
    }

    ngAfterViewInit(): void {
        this.buttonElement = this.tooltip.nativeElement.querySelector('[type="button"]');
        this.tipElement = this.tooltip.nativeElement.querySelector('span[role="tooltip"]');
        this.wrapperElement = this.tooltip.nativeElement;

        fromEvent(this.windowRef, 'resize')
            .pipe(untilDestroyed(this))
            .subscribe(() => this.collisionDetection.detect(this.wrapperElement, this.tipElement));
    }
}
