/* eslint-disable functional/immutable-data */
/* eslint-disable no-null/no-null */
import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import {
    IADSDecoratorsColorsMain,
    IADSDecoratorsSizesChip,
    IADSDecoratorsTypesChip,
} from '../../types';

@Component({
    selector: 'ads-chip',
    templateUrl: './chip.component.html',
    styleUrls: ['./chip.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSChipComponent {
    @Input() avatar = '';
    @Input() color: IADSDecoratorsColorsMain = 'primary';
    @Input() css!: string;
    @Input() disabled = false;
    @Input() flag = '';
    @Input() id!: string;
    @Input() icon = '';
    @Input() label!: string;
    @Input() selected = false;
    @Input() size: IADSDecoratorsSizesChip = 'regular';
    @Input() sublabel!: string;
    @Input() type: IADSDecoratorsTypesChip = 'default';

    @Output() readonly clickEmitter: EventEmitter<Event> = new EventEmitter();

    displayIcon(): boolean {
        return this.icon !== '' && !this.flag && !this.avatar;
    }

    displayAvatar(): boolean {
        return this.avatar !== '' && !this.flag;
    }

    displayFlag(): boolean {
        return this.flag !== '';
    }

    isDisabled(): boolean | null {
        return this.disabled ? true : null;
    }

    click(event: MouseEvent) {
        if (!this.disabled) {
            this.clickEmitter.emit(event);
        }
    }
}
