/* eslint-disable brace-style */
import { DOCUMENT } from '@angular/common';
import {
    AfterContentChecked,
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    forwardRef,
    Inject,
    Input,
    ViewChild,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ADSKeyEvents } from '../../types';
import { CollisionDetectionService, randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-form-dropdown',
    templateUrl: './form-dropdown.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // eslint-disable-next-line @angular-eslint/no-forward-ref
            useExisting: forwardRef(() => ADSFormDropdownComponent),
            multi: true,
        },
    ],
    styleUrls: ['./form-dropdown.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormDropdownComponent
    implements AfterViewInit, ControlValueAccessor, AfterContentChecked
{
    @ViewChild('dropdown') dropdown!: ElementRef;
    @Input() css!: string;
    @Input() disabled = false;
    @Input() hideLabel!: boolean;
    @Input() label = '';
    @Input() ariaLabelledBy = '';
    readonly id = `al-form-dropdown-${randomId()}`;

    private buttonElement!: HTMLButtonElement;
    private inputElement!: HTMLInputElement;
    private optionsElement!: HTMLUListElement;
    private optionsElementItems!: Array<HTMLLIElement>;
    private newValue = '';
    private selectedOption = 0;
    private expanded = false;

    value!: string | null;
    inputValue!: string;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly collisionDetection: CollisionDetectionService,
    ) {}

    buttonClick(): void {
        this.expanded ? this.hide() : this.inputElement.focus();
    }

    show(): void {
        // eslint-disable-next-line functional/immutable-data
        this.expanded = true;
        this.inputElement.setSelectionRange(
            this.inputElement.value.length,
            this.inputElement.value.length,
        );
        this.dropdown.nativeElement.classList.add('is-expanded');
        this.collisionDetection.detect(this.dropdown.nativeElement, this.optionsElement);
    }

    private hide(): void {
        // eslint-disable-next-line functional/immutable-data
        this.expanded = false;
        // this.inputElement.setAttribute('aria-expanded', 'false');
        this.dropdown.nativeElement.classList.remove('is-expanded');
        if (this.value !== this.newValue) {
            // eslint-disable-next-line functional/immutable-data
            this.value = this.newValue;
            this.onChange(this.value);
        }
    }

    private first(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption = 0;
        this.selectOption();
    }

    private last(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption = this.optionsElementItems.length - 1;
        this.selectOption();
    }

    private next(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption =
            this.selectedOption + 1 > this.optionsElementItems.length - 1
                ? 0
                : this.selectedOption + 1;
        this.selectOption();
    }

    private previous(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption =
            this.selectedOption - 1 < 0
                ? this.optionsElementItems.length - 1
                : this.selectedOption - 1;
        this.selectOption();
    }

    private current(item: HTMLLIElement): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption = this.optionsElementItems.indexOf(item);
        this.selectOption();
        this.hide();
    }

    private initialOption(): void {
        if (this.optionsElementItems) {
            this.deselectOption();
            const selected = this.optionsElementItems.find(
                item => item.dataset['value'] === this.value,
            );
            if (selected) {
                // eslint-disable-next-line functional/immutable-data
                this.selectedOption = this.optionsElementItems.indexOf(selected);
                this.selectOption();
            }
        }
    }

    private deselectOption(): void {
        const item: HTMLLIElement | null = this.optionsElementItems[this.selectedOption];
        item?.removeAttribute('aria-selected');
    }

    private selectOption(): void {
        const item: HTMLLIElement | null = this.optionsElementItems[this.selectedOption];

        if (!item?.id) return;

        item?.scrollIntoView({
            behavior: 'smooth',
            block: 'nearest',
            inline: 'start',
        });
        item?.setAttribute('aria-selected', 'true');
        this.inputElement.setAttribute('aria-activedescendant', item.id);
        this.inputElement.setAttribute('value', item.dataset['label'] ?? '');
        // eslint-disable-next-line functional/immutable-data
        this.newValue = item.dataset['value'] ?? '';
    }

    ngAfterContentChecked(): void {
        if (this.dropdown && this.optionsElement) {
            // eslint-disable-next-line functional/immutable-data
            this.optionsElementItems = Array.from(
                this.optionsElement.querySelectorAll('li:not([disabled])'),
            );
            this.optionsElementItems.forEach((item, index) => {
                item.setAttribute('role', 'option');
                item.setAttribute('id', `${this.id}-option-${index}`);
            });
        }
    }

    ngAfterViewInit(): void {
        /* eslint-disable */
        this.buttonElement = this.dropdown.nativeElement.querySelector(`.ads-button`);
        this.inputElement = this.dropdown.nativeElement.querySelector(`#${this.id}`);
        this.optionsElement = this.dropdown.nativeElement.querySelector('ul');
        this.optionsElementItems = Array.from(
            this.optionsElement.querySelectorAll('li:not([disabled])'),
        );
        /* eslint-enable */

        this.optionsElement.setAttribute('tabindex', '-1');
        this.optionsElement.setAttribute('id', `${this.id}-list`);
        this.optionsElement.setAttribute('role', 'listbox');
        this.optionsElementItems.forEach((item, index) => {
            item.setAttribute('role', 'option');
            item.setAttribute('id', `${this.id}-option-${index}`);
        });

        // eslint-disable-next-line no-null/no-null
        if (this.value !== null) {
            this.initialOption();
        } else {
            if (this.inputElement) {
                this.inputElement.setAttribute('value', '');
            }
        }

        fromEvent<KeyboardEvent>(this.inputElement, 'keydown')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe((event: KeyboardEvent) => {
                event.preventDefault();

                if (event.key === ADSKeyEvents.KEY_ARROW_DOWN) {
                    this.next();
                }
                if (event.key === ADSKeyEvents.KEY_ARROW_UP) {
                    this.previous();
                }
                if (event.key === ADSKeyEvents.KEY_HOME) {
                    this.first();
                }
                if (event.key === ADSKeyEvents.KEY_END) {
                    event.preventDefault();
                    this.last();
                }
                if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                    this.initialOption();
                    this.hide();
                    this.inputElement.blur();
                    this.buttonElement.focus();
                }
                if (event.key === ADSKeyEvents.KEY_ENTER) {
                    this.hide();
                    this.inputElement.blur();
                }
                if (event.key === ADSKeyEvents.KEY_TAB) {
                    this.hide();
                    this.inputElement.blur();
                    this.deselectOption();
                    this.buttonElement.focus();
                }
            });

        fromEvent<MouseEvent>(this.optionsElement, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe((event: MouseEvent) => {
                const target = <HTMLLIElement>event.target;

                if (!target.hasAttribute('disabled')) {
                    this.current(target);
                    this.inputElement.blur();
                } else {
                    this.inputElement.focus();
                }
            });

        fromEvent<MouseEvent>(this.document, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe((event: MouseEvent) => {
                const { target } = event;
                if (
                    target instanceof HTMLElement &&
                    !this.dropdown.nativeElement.contains(target)
                ) {
                    this.hide();
                }
            });

        fromEvent(this.windowRef, 'resize')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe(() =>
                this.collisionDetection.detect(this.dropdown.nativeElement, this.optionsElement),
            );
    }

    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: any): void {}

    onTouched(): void {}

    writeValue(value: string | null | undefined) {
        // eslint-disable-next-line no-null/no-null
        if (value !== null && value !== undefined) {
            // eslint-disable-next-line no-null/no-null
            this.value = value === null ? value : value.toString();

            // eslint-disable-next-line no-null/no-null
            if (this.value !== null) {
                this.initialOption();
            } else {
                if (this.inputElement) {
                    this.inputElement.setAttribute('value', '');
                }
            }
        }
    }

    registerOnChange(fn: any) {
        this.onChange = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouched = fn;
    }
    /* eslint-enable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
}
