/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    TemplateRef,
    ViewChild,
} from '@angular/core';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { randomId } from '../../utils';
import { ADSChevronPosition } from '../../types/enums/chevron-position.enum';

@UntilDestroy()
@Component({
    selector: 'ads-accordion',
    templateUrl: './accordion.component.html',
    styleUrls: ['./accordion.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSAccordionComponent implements OnChanges, AfterViewInit {
    @Output() readonly accordionChange: EventEmitter<string> = new EventEmitter<string>();

    @ViewChild('region') region!: ElementRef;
    @ViewChild('content') content!: ElementRef;
    @ViewChild('accordion') templateRef!: TemplateRef<unknown>;

    @Input() css!: string;
    @Input() header!: string;
    @Input() expanded = false;
    @Input() chevronPosition: ADSChevronPosition = ADSChevronPosition.RIGHT;

    readonly id = `ads-accordion-${randomId()}`;
    readonly ADSChevronPosition = ADSChevronPosition;
    regionElement!: HTMLDivElement;
    contentElement!: HTMLDivElement;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        private readonly cdr: ChangeDetectorRef,
    ) {}

    toggle(): void {
        this.expanded = !this.expanded;
        this.accordionChange.emit(this.id);
        this.setHeight();
    }

    close(): void {
        this.expanded = false;
        this.setHeight();
        this.cdr.detectChanges();
    }

    private setHeight(): void {
        const height = this.expanded ? `${this.contentElement?.scrollHeight ?? 0}px` : 0;
        this.regionElement?.setAttribute('style', `height: ${height}`);
    }

    ngAfterViewInit(): void {
        this.regionElement = this.region?.nativeElement;
        this.contentElement = this.content?.nativeElement;

        fromEvent(this.windowRef, 'resize')
            .pipe(untilDestroyed(this))
            .subscribe(() => this.setHeight());
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { expanded } = changes;

        if (expanded) {
            this.expanded = expanded.currentValue;
            this.setHeight();
        }
    }
}
