/* eslint-disable functional/immutable-data */
import { CollisionDetectionService, randomId } from '../../utils';
import { CommonModule, DatePipe, DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    forwardRef,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';

enum DatePickerStatus {
    YEARPICKER = 'yearpicker',
    DAYPICKER = 'daypicker',
    TIMEPICKER = 'timepicker',
}

@UntilDestroy()
@Component({
    selector: 'ads-form-datepicker',
    templateUrl: './form-datepicker.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // eslint-disable-next-line @angular-eslint/no-forward-ref
            useExisting: forwardRef(() => ADSFormDatepickerComponent),
            multi: true,
        },
    ],
    styleUrls: ['./form-datepicker.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormDatepickerComponent implements AfterViewInit, OnChanges {
    @ViewChild('datepicker') datepicker!: ElementRef;
    @ViewChild('daypicker') daypicker!: ElementRef;
    @ViewChild('yearpicker') yearpicker!: ElementRef;
    @Input() css!: string;
    @Input() closedDays!: Array<number> | undefined;
    @Input() disabled = false;
    @Input() label = '';
    @Input() initialDate!: Date | undefined;
    @Input() minDate: Date | undefined;
    @Input() maxDate!: Date | undefined;
    @Input() showYearPicker = true;
    @Input() locale = 'en-US';
    @Input() currentDatePickerStatus: DatePickerStatus = DatePickerStatus.YEARPICKER;
    @Input() enableTimePicker = true;
    @Output() readonly selectDateEmitter = new EventEmitter();

    private datePipe: DatePipe;
    readonly id = `ads-form-datepicker-${randomId()}`;

    private toggleButtonElement!: HTMLButtonElement;
    private inputElement!: HTMLInputElement;
    yearpickerElementItems!: Array<HTMLLIElement>;
    daypickerElementItems!: Array<HTMLLIElement>;
    private readonly newValue = '';
    private selectedOption = 0;
    timerId!: number | undefined;
    private readonly timerInterval = 200;
    startYear = 1926;
    endYear = 2200;
    currentYear = new Date().getFullYear();
    yearChunks!: Array<Array<{ year: number; disabled: boolean }>>;
    currentChunkIndex = 0;

    yearDatepickerStatus = DatePickerStatus.YEARPICKER;
    dayDatepickerStatus = DatePickerStatus.DAYPICKER;
    timeDatepickerStatus = DatePickerStatus.TIMEPICKER;

    // Set an initial selected year (e.g., 2023)
    selectedYear!: number;

    calendar: Array<Record<string, string | null | boolean | Date>> = [];
    expanded = false;
    month!: number;
    year!: number;
    day!: number;
    // use string to display like a clock
    hour = '00';
    minutes = '00';
    value!: string | null;

    // Can be overriden to provide translations
    months: Array<string> = [];
    weekDays: Array<string> = [];

    constructor(
        private readonly commonModule: CommonModule,
        @Inject(WINDOW) private readonly windowRef: Window,
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly collisionDetection: CollisionDetectionService,
        private readonly cdr: ChangeDetectorRef,
    ) {
        this.datePipe = new DatePipe(this.locale);
        this.translateDays();
        this.translateMonths();

        this.month = new Date(Date.now()).getMonth() + 1;
        this.year = new Date(Date.now()).getFullYear();
        this.day = new Date(Date.now()).getDate();

        this.setYearSelector();
        document.body.addEventListener('click', this.handleDocumentClick);
    }

    // --- button events
    setYearSelector() {
        const chunkSize = 25;
        this.yearChunks = [];
        let years: Array<number> = [];
        for (let year = this.endYear; year >= this.startYear; year--) {
            years = years.concat(year);
        }

        for (let i = 0; i < years.length; i += chunkSize) {
            const chunk = years.slice(i, i + chunkSize);
            const formattedChunk = chunk.map(year => {
                return {
                    year,
                    disabled: this.yearIsDisabled(year),
                };
            });
            this.yearChunks = this.yearChunks.concat([formattedChunk]);
            if (chunk.includes(this.currentYear)) {
                this.currentChunkIndex = this.yearChunks.length - 1;
            }
        }
    }

    toggleButtonClick(): void {
        this.expanded ? this.hide() : this.inputElement.focus();
    }

    previousMonthButtonClick(event: Event): void {
        event.preventDefault();
        event.stopPropagation();
        this.selectedOption = 0;
        this.year = this.month - 1 < 1 ? this.year - 1 : this.year;
        this.month = this.month - 1 < 1 ? 12 : this.month - 1;
        this.createCalendar();
        this.selectOption();
    }

    nextMonthButtonClick(event: Event): void {
        event.preventDefault();
        event.stopPropagation();
        this.selectedOption = 0;
        this.year = this.month + 1 > 12 ? this.year + 1 : this.year;
        this.month = this.month + 1 > 12 ? 1 : this.month + 1;
        this.createCalendar();
        this.selectOption();
    }

    // --- toggle

    show(): void {
        this.createCalendar();
        this.expanded = true;
        this.cdr.detectChanges();
    }

    private hide(): void {
        this.expanded = false;

        if (this.value !== this.newValue) {
            this.value = this.newValue;
            this.onChange(this.value);
        }
        this.cdr.detectChanges();
    }

    private initialOption(): void {
        if (this.isViewInitialized()) {
            this.createCalendar();
            this.selectOption();
        }
    }

    private selectOption(): void {
        if (!isNaN(this.year) && !isNaN(this.month) && !isNaN(this.day)) {
            const fullDate = new Date(
                this.year,
                this.month - 1,
                this.day,
                parseInt(this.hour),
                parseInt(this.minutes),
            );
            const formattedDate = this.datePipe.transform(
                fullDate,
                this.enableTimePicker ? 'short' : 'mediumDate',
            );

            this.inputElement.setAttribute('value', formattedDate ?? '');
            this.inputElement.focus();
            this.selectDateEmitter.emit(fullDate);
        }
    }

    // --- utilities

    private createCalendar(): void {
        this.calendar = [];
        let date: Date;
        const currentMonthLength: number = new Date(this.year, this.month, 0).getDate();
        const previousMonthLength: number = new Date(this.year, this.month - 1, 0).getDate();
        const firstDay: number = new Date(`${this.year}-${this.month}-01`).getDay();
        const lastDay: number = new Date(
            `${this.year}-${this.month}-${currentMonthLength}`,
        ).getDay();

        // previous month
        for (let i = firstDay; i > 0; i--) {
            const day = previousMonthLength - i + 1;
            date = new Date(this.year, this.month - 2, day); // use new Date to instantiate a new date object with the required year, month, and day
            this.calendar.push(this.calendarDay(date, day));
        }

        // current month
        for (let i = 1; i <= currentMonthLength; i++) {
            date = new Date(this.year, this.month - 1, i); // use new Date to instantiate a new date object with current year, month, and i as day
            this.calendar.push(this.calendarDay(date, i, !this.isDateAvailable(date), true));
        }

        // next month
        for (let i = 1; i <= 6 - lastDay; i++) {
            date = new Date(this.year, this.month, i); // use new Date to instantiate a new date object with the required year, month, and day
            this.calendar.push(this.calendarDay(date, i));
        }

        this.cdr.detectChanges();
    }

    private calendarDay(
        date: Date,
        day: number,
        disabled: boolean = true,
        currentMonth: boolean = false,
    ): Record<string, string | null | boolean | Date> {
        const d = new Date(date);

        return {
            value: date,
            displayValue: String(day),
            longDate: `${d.getDate()} ${this.months[d.getMonth()]}, ${d.getFullYear()}`,
            fullDate: this.getFormattedDate(date, 'medium'),
            today: this.isToday(date),
            weekend: this.isWeekend(date),
            disabled: disabled,
            closed: this.isClosed(date),
            currentMonth: currentMonth,
            selected: this.isDaySelected(date),
        };
    }

    private isToday(date: Date): boolean {
        const today = new Date();
        const compareDate = new Date(date);

        return (
            compareDate.getDate() === today.getDate() &&
            compareDate.getMonth() === today.getMonth() &&
            compareDate.getFullYear() === today.getFullYear()
        );
    }
    private isDaySelected(date: Date): boolean {
        const { year, month, day } = this;
        const selectedDay = year && month && day ? new Date(year, month - 1, day) : undefined;
        selectedDay?.setHours(0, 0, 0, 0);
        date.setHours(0, 0, 0, 0);

        return selectedDay?.getTime() === date.getTime();
    }

    private isWeekend(date: Date): boolean {
        return new Date(date).getDay() % 6 === 0;
    }

    private isDateAvailable(date: string | Date): boolean {
        const compareDate = new Date(date);
        let available = true;

        if (this.minDate) {
            available = compareDate.getTime() >= new Date(this.minDate).getTime();
        }

        if (this.maxDate && available) {
            available = compareDate.getTime() <= new Date(this.maxDate).getTime();
        }

        if (available) {
            available = !this.isClosed(date);
        }

        return available;
    }

    private isClosed(date: string | Date): boolean {
        if (this.closedDays) {
            let day = new Date(date).getDay();
            // conversion for zero based getDay() result (sunday === 0)
            day = day !== 0 ? day - 1 : 6;

            return this.closedDays.indexOf(day) > -1;
        }

        return false;
    }

    private getFormattedDate(date: Date, format: string): string | null {
        return this.datePipe.transform(date.getTime(), format);
    }

    private getSelectedDate(): string {
        return this.yearpickerElementItems[this.selectedOption]?.dataset['value'] ?? '';
    }

    trackByIndex(index: number): number {
        return index;
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { closedDays, initialDate, minDate, maxDate, enableTimePicker, locale } = changes;

        if (closedDays) {
            this.closedDays = closedDays.currentValue;
        }
        if (enableTimePicker) {
            this.enableTimePicker = enableTimePicker.currentValue;
        }

        if (minDate) {
            this.minDate = minDate.currentValue;

            this.setYearSelector();
        }
        if (locale) {
            this.locale = locale.currentValue;
            this.datePipe = new DatePipe(this.locale);
            this.translateDays();
            this.translateMonths();
        }

        if (maxDate) {
            if (maxDate.currentValue instanceof Date) {
                this.maxDate;
            } else {
                this.maxDate = maxDate.currentValue;
            }
            this.setYearSelector();
        }

        if (initialDate) {
            this.initialDate = initialDate.currentValue;

            this.setDate(this.initialDate);
        }
    }

    setDate(dateToSet: string | Date | undefined) {
        if (dateToSet && this.isDateAvailable(dateToSet)) {
            const date = new Date(dateToSet);
            this.year = date.getFullYear();
            this.month = date.getMonth() + 1;
            this.day = date.getDate();
            this.hour = this.formatNumber(date.getHours());
            this.minutes = this.formatNumber(date.getMinutes());
            date.setHours(parseInt(this.hour));
            date.setMinutes(parseInt(this.minutes));
            date.setSeconds(0);
            date.setMilliseconds(0);
            this.createCalendar();
            //  this.cdr.detectChanges();
        }
    }

    ngAfterViewInit(): void {
        this.inputElement = this.datepicker.nativeElement.querySelector(`#${this.id}`);
        this.toggleButtonElement = this.datepicker?.nativeElement?.querySelector(
            `#al-button-${this.id}-button-toggle`,
        );

        fromEvent(this.windowRef, 'resize')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe(() =>
                this.collisionDetection.detect(
                    this.datepicker.nativeElement,
                    this.yearpicker?.nativeElement
                        ? this.yearpicker.nativeElement
                        : this.daypicker.nativeElement,
                ),
            );
        this.initialOption();
    }

    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: any): void {}
    onKeyDownInputElement(event: KeyboardEvent) {}
    onDayPickerClick(event: MouseEvent, date: Date | string | null | boolean) {
        const target = <HTMLLIElement>event.target;

        if (!target.hasAttribute('disabled') && date && typeof date !== 'boolean') {
            const chosenDate = new Date(date);
            this.day = chosenDate.getDate();
            chosenDate.setFullYear(this.year);
            chosenDate.setMonth(this.month);
            chosenDate.setDate(this.day);
            chosenDate.setHours(parseInt(this.hour));
            chosenDate.setMinutes(parseInt(this.minutes));

            this.writeValue(chosenDate);
            this.day = new Date(chosenDate).getDate();
            this.inputElement.blur();
            // hide if timepicker is disabled
            this.enableTimePicker
                ? (this.currentDatePickerStatus = this.timeDatepickerStatus)
                : this.hide();
            this.cdr.detectChanges();
        } else {
            this.inputElement.focus();
        }
    }

    onTouched(): void {}

    writeValue(value: string | Date | null | undefined) {
        // eslint-disable-next-line no-null/no-null
        if (value !== null && value !== undefined) {
            if (value instanceof Date) {
                this.value = value.toDateString();
            } else {
                this.value = value;
            }

            // eslint-disable-next-line no-null/no-null
            if (this.value) {
                this.selectOption();
            } else {
                if (this.inputElement) {
                    this.inputElement.setAttribute('value', '');
                }
            }
        }
    }

    private isViewInitialized() {
        return this.yearpicker;
    }
    selectYear(year: number) {
        this.selectedYear = year;
        this.month = this.month ? this.month : 1;
        if (year) {
            this.year = year;
            if (this.maxDate && this.maxDate.getFullYear() === year) {
                this.month = this.maxDate.getMonth() + 1;
            }
            if (this.minDate && this.minDate.getFullYear() === year) {
                this.month = this.minDate.getMonth() + 1;
            }
        }
        this.createCalendar();
        this.currentDatePickerStatus = this.dayDatepickerStatus;
        this.selectOption();
        this.cdr.detectChanges();
    }
    previousYearChunk() {
        this.currentChunkIndex -= 1;
    }
    nextYearChunk() {
        this.currentChunkIndex += 1;
    }
    yearIsDisabled(year: number) {
        if (this.minDate && this.minDate.getFullYear() > year) {
            return true;
        }
        if (this.maxDate && this.maxDate.getFullYear() < year) {
            return true;
        }

        return false;
    }
    private translateDays(): void {
        // array representing days in epoch
        const days = [
            1696774522000, 1696860922000, 1696947322000, 1697033722000, 1697120122000,
            1697194707000, 1696688122000,
        ];
        this.weekDays = days.map(day => this.datePipe.transform(`${day}`, 'EEEE') ?? '');
    }
    private translateMonths(): void {
        for (let month = 0; month < 12; month++) {
            const date = new Date();
            date.setMonth(month);
            const monthName = this.datePipe.transform(date, 'MMMM');
            if (monthName) {
                this.months = this.months.concat(monthName);
            }
        }
    }
    onBlurHours() {
        const value = parseInt(this.hour, 10);
        if (!value) {
            this.hour = this.formatNumber(0);
        } else if (value > 23) {
            this.hour = this.formatNumber(23);
        } else {
            this.hour = this.formatNumber(value);
        }
        this.cdr.detectChanges();
    }
    onBlurMinutes() {
        const value = parseInt(this.minutes, 10);
        if (!value) {
            this.minutes = this.formatNumber(0);
        } else if (value > 59) {
            this.minutes = this.formatNumber(59);
        } else {
            this.minutes = this.formatNumber(value);
        }
        this.cdr.detectChanges();
    }

    onInput(event: any) {
        const newValue = event.target.value.replace(/[^0-9]/g, '');
        event.target.value = newValue;
    }

    private formatNumber(value: number): string {
        // locale does not really matter here as no decimals will be allowed
        return value.toLocaleString('en-US', { minimumIntegerDigits: 2, useGrouping: false });
    }
    handleHourIncrement() {
        const value = parseInt(this.hour, 10);
        if (value < 23) {
            this.hour = this.formatNumber(value + 1);
        } else {
            this.hour = this.formatNumber(0);
        }
        this.cdr.detectChanges();
    }

    handleHourDecrement() {
        const value = parseInt(this.hour, 10);
        if (value > 0) {
            this.hour = this.formatNumber(value - 1);
        } else {
            this.hour = this.formatNumber(23);
        }
        this.cdr.detectChanges();
    }
    handleMinutesIncrement() {
        const value = parseInt(this.minutes, 10);
        if (value < 59) {
            this.minutes = this.formatNumber(value + 1);
        } else {
            this.minutes = this.formatNumber(0);
        }
        this.cdr.detectChanges();
    }

    handleMinutesDecrement() {
        const value = parseInt(this.minutes, 10);
        if (value > 0) {
            this.minutes = this.formatNumber(value - 1);
        } else {
            this.minutes = this.formatNumber(59);
        }
        this.cdr.detectChanges();
    }
    startTimer(direction: string, timeUnit: string) {
        // start the timer if it's not already running
        if (!this.timerId) {
            this.timerId = window.setInterval(() => {
                if (timeUnit === 'hours') {
                    if (direction === 'increment') {
                        this.handleHourIncrement();
                    } else {
                        this.handleHourDecrement();
                    }
                } else if (timeUnit === 'minutes') {
                    if (direction === 'increment') {
                        this.handleMinutesIncrement();
                    } else {
                        this.handleMinutesDecrement();
                    }
                }
            }, this.timerInterval);
        }
    }

    stopTimer() {
        if (this.timerId) {
            clearInterval(this.timerId);
            this.timerId = undefined;
        }
    }
    handleDocumentClick = (event: MouseEvent) => {
        // Check if the click event target is inside the datepicker or any of its child elements
        const clickedInsideDatepicker = this.datepicker.nativeElement.contains(event.target);
        if (!clickedInsideDatepicker) {
            // Check if the clicked element or any of its parent elements are still visible in the DOM as you switch from year to day to hour
            if (this.document.body.contains(<Node>event.target)) {
                this.hide();
            }
        }
    };
    onTimeSubmit() {
        const date = new Date();
        date.setFullYear(this.year);
        date.setMonth(this.month);
        date.setDate(this.day);
        date.setHours(parseInt(this.hour));
        date.setMinutes(parseInt(this.minutes));
        this.writeValue(date);
        this.day = new Date(date).getDate();
        this.inputElement.blur();
        this.currentDatePickerStatus = this.timeDatepickerStatus;
        this.cdr.detectChanges();
        this.hide();
    }
}
