import { ADSKeyEvents, ADSStatus } from '../../types';
import { CollisionDetectionService, randomId } from '../../utils';
import { DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    forwardRef,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { debounceTime, filter } from 'rxjs/operators';

@UntilDestroy()
@Component({
    selector: 'ads-form-typeahead',
    templateUrl: './typeahead.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // eslint-disable-next-line @angular-eslint/no-forward-ref
            useExisting: forwardRef(() => ADSFormTypeaheadComponent),
            multi: true,
        },
    ],
    styleUrls: ['./typeahead.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormTypeaheadComponent implements AfterViewInit, ControlValueAccessor, OnChanges {
    @ViewChild('typeahead') typeahead!: ElementRef;
    @Input() async = 'false';
    @Input() css!: string;
    @Input() hint!: string;
    @Input() label = '';
    @Input() type = 'text';
    @Input() placeholder = '';
    @Input() displayNoResults = true;
    @Input() displaySearchIcon = true;
    @Output() private readonly selectedValue = new EventEmitter();
    @Output() readonly focusEmitter = new EventEmitter();
    @Output() readonly blurEmitter = new EventEmitter();
    readonly id = `ads-form-typeahead-${randomId()}`;

    inputElement!: HTMLInputElement;
    private optionsElement!: HTMLUListElement;
    private optionsElementItems!: Array<HTMLLIElement>;

    private selectedOption = -1;
    private expanded = false;
    private value!: string | null;
    displayNoResultsElement = false;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly cdr: ChangeDetectorRef,
        private readonly collisionDetection: CollisionDetectionService,
    ) {}

    onFocus() {
        this.focusEmitter.emit();
    }

    onBlur() {
        this.blurEmitter.emit();
    }

    private show(): void {
        // eslint-disable-next-line functional/immutable-data
        this.expanded = true;
        this.inputElement.setAttribute('aria-expanded', 'true');
        this.typeahead.nativeElement.classList.add('is-expanded');
        this.collisionDetection.detect(this.typeahead.nativeElement, this.optionsElement);
    }

    private hide(): void {
        // eslint-disable-next-line functional/immutable-data
        this.expanded = false;
        this.inputElement.setAttribute('aria-expanded', 'false');
        this.typeahead.nativeElement.classList.remove('is-expanded');
    }

    private first(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption = 0;
        this.selectOption();
    }

    private last(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption = this.optionsElementItems.length - 1;
        this.selectOption();
    }

    private next(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption =
            this.selectedOption + 1 > this.optionsElementItems.length - 1
                ? 0
                : this.selectedOption + 1;
        this.selectOption();
    }

    private previous(): void {
        this.deselectOption();
        // eslint-disable-next-line functional/immutable-data
        this.selectedOption =
            this.selectedOption - 1 < 0
                ? this.optionsElementItems.length - 1
                : this.selectedOption - 1;
        this.selectOption();
    }

    private selectOption(): void {
        const item: HTMLLIElement | null = this.optionsElementItems[this.selectedOption];
        item?.setAttribute('aria-selected', 'true');
        this.inputElement.setAttribute('aria-activedescendant', item.id);
        this.scrollIntoView();
    }

    private deselectOption(): void {
        const item: HTMLLIElement | null = this.optionsElementItems[this.selectedOption];
        item?.removeAttribute('aria-selected');
        this.inputElement.removeAttribute('aria-activedescendant');
    }

    private emitOption(item: HTMLLIElement): void {
        if (item?.dataset['value']) {
            // eslint-disable-next-line functional/immutable-data
            this.inputElement.value = item.dataset['label'] ?? '';
            this.selectedValue.emit(item.dataset['value']);
            this.onChange(this.inputElement.value);
        }
    }

    private scrollIntoView(): void {
        if (this.optionsElementItems.length > 0 && this.selectedOption > -1) {
            this.optionsElementItems[this.selectedOption].scrollIntoView({
                behavior: 'smooth',
                block: 'nearest',
                inline: 'start',
            });
        }
    }

    private renderOptions(): void {
        // eslint-disable-next-line functional/immutable-data
        this.optionsElementItems = Array.from(
            this.optionsElement.querySelectorAll('li:not([disabled])'),
        );

        const selectableElementItems = Array.from(this.optionsElement.querySelectorAll('li'));

        selectableElementItems.length < 1
            ? this.optionsElement.classList.add('has-no-results')
            : this.optionsElement.classList.remove('has-no-results');

        // eslint-disable-next-line functional/immutable-data
        this.selectedOption = -1;
        selectableElementItems.forEach((item, index) => {
            if (
                this.inputElement.value &&
                item.outerText.localeCompare(this.inputElement.value) === 0
            ) {
                // eslint-disable-next-line functional/immutable-data
                this.selectedOption = index;
            }
            item.setAttribute('role', 'option');
            item.setAttribute('id', `${this.id}-option-${index}`);
        });

        if (selectableElementItems.length > 0) {
            if (this.selectedOption !== -1) {
                this.selectOption();
            } else {
                this.first();
            }
        }
    }

    clearInput(): void {
        /* eslint-disable */
        this.inputElement.value = '';
        this.displayNoResultsElement = false;
        this.onChange('');
        this.inputElement.blur();
        // Angular needs this :(
        setTimeout(() => this.inputElement.focus(), 0);
        /* eslint-enable */
    }

    ngAfterViewInit() {
        /* eslint-disable */
        this.inputElement = this.typeahead.nativeElement.querySelector(`#${this.id}`);
        this.inputElement = this.typeahead.nativeElement.querySelector(`#${this.id}`);
        this.optionsElement = this.typeahead.nativeElement.querySelector('ul');
        /* eslint-enable */

        this.optionsElement.setAttribute('tabindex', '-1');
        this.optionsElement.setAttribute('id', `${this.id}-list`);
        this.optionsElement.setAttribute('role', 'listbox');
        this.renderOptions();

        fromEvent<KeyboardEvent>(this.inputElement, 'keydown')
            .pipe(untilDestroyed(this))
            .subscribe((event: KeyboardEvent) => {
                if (
                    this.optionsElementItems.length > 0 &&
                    (this.async === 'false' || this.async === ADSStatus.SUCCESS)
                ) {
                    if (event.key === ADSKeyEvents.KEY_ARROW_DOWN) {
                        event.preventDefault();
                        this.next();
                    }
                    if (event.key === ADSKeyEvents.KEY_ARROW_UP) {
                        event.preventDefault();
                        this.previous();
                    }
                    if (event.key === ADSKeyEvents.KEY_HOME) {
                        event.preventDefault();
                        this.first();
                    }
                    if (event.key === ADSKeyEvents.KEY_END) {
                        event.preventDefault();
                        this.last();
                    }
                }

                if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                    event.preventDefault();
                    this.hide();
                    this.inputElement.blur();
                }
                if (event.key === ADSKeyEvents.KEY_ENTER) {
                    event.preventDefault();
                    this.emitOption(this.optionsElementItems[this.selectedOption]);
                    this.hide();
                    this.inputElement.blur();
                }
                if (event.key === ADSKeyEvents.KEY_TAB) {
                    this.hide();
                    this.inputElement.blur();
                }
            });

        fromEvent<KeyboardEvent>(this.inputElement, 'keyup')
            .pipe(untilDestroyed(this), debounceTime(200))
            .subscribe((event: KeyboardEvent) => {
                if (
                    event.key !== ADSKeyEvents.KEY_ARROW_DOWN &&
                    event.key !== ADSKeyEvents.KEY_ARROW_UP &&
                    event.key !== ADSKeyEvents.KEY_ARROW_LEFT &&
                    event.key !== ADSKeyEvents.KEY_ARROW_RIGHT &&
                    event.key !== ADSKeyEvents.KEY_HOME &&
                    event.key !== ADSKeyEvents.KEY_END &&
                    event.key !== ADSKeyEvents.KEY_ESCAPE &&
                    event.key !== ADSKeyEvents.KEY_ENTER &&
                    event.key !== ADSKeyEvents.KEY_TAB
                ) {
                    this.deselectOption();
                    // eslint-disable-next-line functional/immutable-data
                    this.selectedOption = -1;
                    this.optionsElement.classList.remove('has-no-results');
                    this.onChange(this.inputElement.value);

                    if (this.async === 'false') {
                        // Angular needs this :(
                        setTimeout(() => {
                            this.renderOptions();
                            // eslint-disable-next-line functional/immutable-data
                            this.displayNoResultsElement =
                                this.inputElement.value.length > 1 &&
                                this.optionsElementItems.length < 1
                                    ? true
                                    : false;
                            this.cdr.detectChanges();
                        }, 0);
                    }
                }
            });

        fromEvent<FocusEvent>(this.inputElement, 'focus')
            .pipe(untilDestroyed(this))
            .subscribe(() => {
                this.deselectOption();
                // eslint-disable-next-line functional/immutable-data
                this.selectedOption = -1;
                setTimeout(() => this.show());
                setTimeout(() => this.renderOptions(), 0);
            });

        fromEvent<MouseEvent>(this.optionsElement, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe((event: MouseEvent) => {
                const target = <HTMLLIElement>event.target;

                if (!target.hasAttribute('disabled')) {
                    this.emitOption(target);
                    this.hide();
                } else {
                    this.inputElement.focus();
                }
            });

        fromEvent<MouseEvent>(this.document, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe((event: MouseEvent) => {
                const { target } = event;
                if (
                    target instanceof HTMLElement &&
                    !this.typeahead.nativeElement.contains(target)
                ) {
                    this.hide();
                }
            });

        fromEvent(this.windowRef, 'resize')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe(() =>
                this.collisionDetection.detect(this.typeahead.nativeElement, this.optionsElement),
            );
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { async } = changes;

        if (
            async &&
            (async.currentValue === ADSStatus.SUCCESS ||
                async.currentValue === ADSStatus.ERROR ||
                async.currentValue === ADSStatus.IDLE)
        ) {
            // Angular needs this :(
            setTimeout(() => {
                this.renderOptions();
                // eslint-disable-next-line functional/immutable-data
                this.displayNoResultsElement =
                    this.inputElement.value.length > 1 && this.optionsElementItems.length < 1
                        ? true
                        : false;
                this.cdr.detectChanges();
            }, 0);
        }
    }

    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: any): void {}

    onTouched(): void {}

    writeValue(value: string | null) {
        this.value = value;
        if (this.inputElement) {
            this.inputElement.value = this.value ?? '';
        }
        this.onChange(this.value);
    }

    registerOnChange(fn: any) {
        this.onChange = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouched = fn;
    }
    /* eslint-enable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
}
