/* eslint-disable functional/immutable-data */
import { DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { filter, fromEvent } from 'rxjs';
import { ADSKeyEvents } from '../../types';
import { focusTrap, randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-popup',
    templateUrl: './popup.component.html',
    styleUrls: ['./popup.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSPopUpComponent implements OnChanges, AfterViewInit {
    @Output() readonly closeEmitter: EventEmitter<boolean> = new EventEmitter();
    @ViewChild('popup') popup!: ElementRef;

    @Input() labelClose = 'Close popup';
    @Input() openerId!: string;
    @Input() title!: string;
    @Input() visible = false;

    css: 'is-visible' | 'is-hidden' = 'is-hidden';
    readonly id = `popup-${randomId()}`;

    constructor(
        private readonly cdr: ChangeDetectorRef,
        @Inject(DOCUMENT) private readonly document: Document,
    ) {}

    openPopUp(): void {
        this.visible = true;
        this.cdr.detectChanges();

        setTimeout(() => {
            this.css = 'is-visible';
            this.cdr.detectChanges();
            this.focusOnCloser();
        }, 1);
    }

    closePopUp(): void {
        this.css = 'is-hidden';
        this.cdr.detectChanges();

        setTimeout(() => {
            this.visible = false;
            this.cdr.detectChanges();
            this.closeEmitter.emit(true);
            this.focusOnOpener();
        }, 300);
    }

    focusOnCloser(): void {
        this.popup.nativeElement.querySelector(`#ads-button-${this.id}`)?.focus();
    }

    focusOnOpener(): void {
        if (this.openerId) {
            this.document.getElementById(this.openerId)?.focus();
        }
    }

    ngAfterViewInit(): void {
        // focus trap & escape
        fromEvent<KeyboardEvent>(this.popup.nativeElement, 'keydown')
            .pipe(
                untilDestroyed(this),
                filter(() => this.visible),
            )
            .subscribe(event => {
                if (event.key === ADSKeyEvents.KEY_TAB) {
                    focusTrap(event, this.popup.nativeElement);
                }
                if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                    this.closePopUp();
                }
            });

        // click outside
        fromEvent<MouseEvent>(this.popup.nativeElement, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.visible),
            )
            .subscribe((event: MouseEvent) => {
                const target = event.target as Element;
                if (!target.closest('.ads-popup')) {
                    this.closePopUp();
                }
            });
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { visible, title, openerId } = changes;

        if (visible && visible.currentValue) {
            visible.currentValue === true ? this.openPopUp() : this.closePopUp();
        }

        if (title && title.currentValue) {
            this.title = title.currentValue;
            this.cdr.detectChanges();
        }

        if (openerId && openerId.currentValue) {
            this.openerId = openerId.currentValue;
        }
    }
}
