/* eslint-disable functional/immutable-data */
/* eslint-disable @typescript-eslint/no-non-null-assertion */
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    Input,
    OnChanges,
    SimpleChanges,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ChangeEvent } from '@ckeditor/ckeditor5-angular';
import DecoupledEditor from '@ckeditor/ckeditor5-build-decoupled-document';
import { EditorConfig } from '@ckeditor/ckeditor5-core';
@Component({
    selector: 'ads-form-html',
    templateUrl: './form-html.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: ADSFormHtmlComponent,
            multi: true,
        },
    ],
    styleUrls: ['./form-html.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormHtmlComponent implements OnChanges, ControlValueAccessor {
    constructor(private readonly cdr: ChangeDetectorRef) {}
    value = '';
    @Input() content!: string;
    @Input() placeholder!: string;
    disabled = false;
    touched = false;

    Editor = DecoupledEditor;
    config: EditorConfig = {
        placeholder: '',
        toolbar: {
            items: ['bold', 'italic', 'underline', 'link', 'bulletedList'],
        },
        list: {
            properties: {
                styles: false,
                startIndex: true,
                reversed: true,
            },
        },
        link: {
            addTargetToExternalLinks: true,
            decorators: {
                isExternal: {
                    mode: 'manual',
                    label: 'External link?',
                    attributes: {
                        target: '_blank',
                        rel: 'noopener noreferrer',
                    },
                },
            },
        },
        ui: {
            poweredBy: {
                // eslint-disable-next-line no-null/no-null
                label: null,
                horizontalOffset: 0,
                verticalOffset: 0,
                position: 'border',
                side: 'right',
            },
        },
    };

    ngOnChanges(changes: SimpleChanges): void {
        const { placeholder } = changes;

        if (placeholder) {
            // eslint-disable-next-line functional/immutable-data
            this.placeholder = placeholder.currentValue;
            this.config = {
                ...this.config,
                placeholder: this.placeholder,
            };
            this.cdr.detectChanges();
        }
    }
    onReady(editor: DecoupledEditor): void {
        const element = editor.ui.getEditableElement()!;
        const parent = element.parentElement!;

        parent.insertBefore(editor.ui.view.toolbar.element!, element);
    }
    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: string): void {}

    onTouched(): void {}

    registerOnChange(fn: any): void {
        this.onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }

    markAsTouched() {
        if (!this.touched) {
            this.onTouched();
            this.touched = true;
        }
    }

    setDisabledState(disabled: boolean) {
        this.disabled = disabled;
    }

    writeValue(value: string): void {
        this.value = value;
    }

    onEditorChange({ editor }: ChangeEvent) {
        if (!this.disabled && editor) {
            const editorValue = editor.data.get();
            this.value = editorValue;
            this.onChange(this.value);
        }
    }
}
