import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    Input,
    OnChanges,
    SimpleChanges,
    TemplateRef,
    ViewChild,
} from '@angular/core';

@Component({
    selector: 'ads-step',
    templateUrl: './step.component.html',
    styleUrls: ['./step.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSStepComponent implements OnChanges {
    @Input() css!: string;
    @Input() label!: string;
    @Input() number!: string;
    @Input() disabled = false;
    @Input() finished = false;
    @Input() errors = false;
    @Input() hidden = false;
    @Input() selected = false;

    @ViewChild('template') readonly template!: TemplateRef<unknown>;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    ngOnChanges(changes: SimpleChanges): void {
        const { disabled, errors, finished, hidden, selected, label } = changes;

        if (disabled) {
            // eslint-disable-next-line functional/immutable-data
            this.disabled = disabled.currentValue;
            this.cdr.detectChanges();
        }

        if (errors) {
            // eslint-disable-next-line functional/immutable-data
            this.errors = errors.currentValue;
            this.cdr.detectChanges();
        }

        if (finished) {
            // eslint-disable-next-line functional/immutable-data
            this.finished = finished.currentValue;
            this.cdr.detectChanges();
        }

        if (hidden) {
            // eslint-disable-next-line functional/immutable-data
            this.hidden = hidden.currentValue;
            this.cdr.detectChanges();
        }

        if (selected) {
            // eslint-disable-next-line functional/immutable-data
            this.selected = selected.currentValue;
            this.cdr.detectChanges();
        }

        if (label) {
            // eslint-disable-next-line functional/immutable-data
            this.label = label.currentValue;
            this.cdr.detectChanges();
        }
    }
}
