/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    Output,
    ViewChild,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { filter, fromEvent } from 'rxjs';
import { MIMETypes } from '../../types';
import { formatToBytes } from '../../utils';
import { ADSConfirmationBoxComponent } from '../confirmation-box/confirmation-box.component';

@UntilDestroy()
@Component({
    selector: 'ads-form-input-files',
    templateUrl: './form-input-files.component.html',
    styleUrls: ['./form-input-files.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormInputFilesComponent implements AfterViewInit {
    @ViewChild('fileInput') fileInput!: ElementRef;
    @ViewChild('dropzone') dropzone!: ElementRef;
    @ViewChild('alertModal') alertModal!: ADSConfirmationBoxComponent;

    @Output() readonly filesEmitter: EventEmitter<Array<File>> = new EventEmitter();

    @Input() acceptedFileTypes = '.doc, .docx, .jpg, .jpeg, .png, .pdf, .xls, .xlsx, .zip';
    @Input() css!: string;
    @Input() fileLimit = 0;
    @Input() sizeLimit = 0;
    @Input() disabled = false;
    @Input() id!: string;
    @Input() label!: string;
    @Input() description!: string;
    @Input() removeAllFilesEmitter?: EventEmitter<void>;
    @Input() files: Array<File> = [];

    private dropzoneElement!: HTMLDivElement;
    private inputElement!: HTMLInputElement;
    private readonly fileIcons: Record<string, string> = {
        default: 'optimal_representation',
        pdf: 'download_pdf',
        ppt: 'download_ppt',
        xls: 'download_xls',
        'vnd.openxmlformats-officedocument.spreadsheetml.sheet': 'download_xls',
        'x-zip-compressed': 'download_zip',
    };
    private readonly mimeTypes = MIMETypes;

    dragover = false;
    focus = false;
    acceptedMimeTypes: Array<string> = [];
    fileLimitReached = false;
    sizeLimitReached = false;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    // --- file properties

    getFileType(type: string): string {
        return type ? type.split('/')[1].toLowerCase() ?? '' : '';
    }

    getFileIcon(type: string): string {
        return this.fileIcons[this.getFileType(type)] ?? this.fileIcons['default'];
    }

    getFormattedFileSize(size: number): string {
        return formatToBytes(size, 0);
    }

    getFormattedTotalFilesSize(): string {
        const total = this.files.reduce((acc, file) => acc + file.size, 0);

        return formatToBytes(total, 0);
    }

    getFormattedSizeLimit(): string {
        return formatToBytes(this.sizeLimit, 0);
    }

    // -- mime types

    setAcceptedMimeTypes(): void {
        this.acceptedMimeTypes =
            this.acceptedFileTypes !== ''
                ? [
                      ...new Set(
                          Object.entries(this.mimeTypes)
                              .filter(([key]) => this.acceptedFileTypes.indexOf(key) > -1)
                              .map(item => item[1]),
                      ),
                  ]
                : [...new Set(Object.entries(this.mimeTypes).map(item => item[1]))];
    }

    // --- fileList checks

    checkFileType(type: string): boolean {
        return this.acceptedMimeTypes.indexOf(type) > -1;
    }

    checkFileLimit(length: number): boolean {
        return +this.fileLimit > 0 && +this.fileLimit <= length;
    }

    checkFileLimitReached(): void {
        this.fileLimitReached = this.checkFileLimit(this.files.length);
    }

    checkSizeLimit(list: Array<File>, size: number): boolean {
        const total = list.reduce((acc, file) => acc + file.size, 0);

        return +this.sizeLimit > 0 ? total + size > +this.sizeLimit : false;
    }

    checkSizeLimitReached(): void {
        this.sizeLimitReached = this.checkSizeLimit(this.files, 500000);
    }

    checkDuplicates(list: Array<File>, name: string): boolean {
        return list.find(item => item.name === name) ? true : false;
    }

    // --- filelist updates

    removeFile(filename: string): void {
        this.files = Array.from(this.files).filter(item => item.name !== filename);
        this.checkFileLimitReached();
        this.checkSizeLimitReached();
        this.filesEmitter.emit(this.files);
        this.cdr.detectChanges();
    }
    // .msg/otf formats are not recognized by browsers yet leaving their mimetypes empty
    getFileTypeFromName(filename: string) {
        if (filename.toLowerCase().endsWith('.msg')) {
            return this.mimeTypes['.msg'];
        } else if (filename.toLowerCase().endsWith('.oft')) {
            return this.mimeTypes['.oft'];
        }

        return '';
    }

    addFiles(files: FileList): void {
        const dataTransfer = new DataTransfer();

        for (const currentFile of Array.from(this.files)) {
            dataTransfer.items.add(currentFile as unknown as File);
        }

        if (files.length) {
            for (const file of Array.from(files)) {
                const fileLimit = this.checkFileLimit(dataTransfer.items.length);
                const fileType = this.checkFileType(
                    file.type || this.getFileTypeFromName(file.name),
                );
                const sizeLimit = this.checkSizeLimit(Array.from(dataTransfer.files), file.size);
                const duplicate = this.checkDuplicates(Array.from(dataTransfer.files), file.name);

                if (fileLimit) {
                    this.alertModal.show(
                        '',
                        $localize`:@@ADSUIFileErrorLimit: You've reached the maximum amount of files`,
                    );
                } else if (!fileType) {
                    this.alertModal.show(
                        '',
                        $localize`:@@ADSUIFileErrorType: File format not supported`,
                    );
                } else if (sizeLimit) {
                    this.alertModal.show(
                        '',
                        $localize`:@@ADSUIFileErrorSize: File size exceeds the maximum limit`,
                    );
                } else if (duplicate) {
                    this.alertModal.show(
                        '',
                        $localize`:@@ADSUIFileErrorDuplicate: A duplicate file has been detected`,
                    );
                } else {
                    dataTransfer.items.add(file as unknown as File);
                }
            }
        }

        this.files = Array.from(dataTransfer.files);
        this.checkFileLimitReached();
        this.checkSizeLimitReached();
        this.filesEmitter.emit(this.files);
    }

    // -- View Init

    ngAfterViewInit(): void {
        if (!this.disabled) {
            if (this.removeAllFilesEmitter) {
                this.removeAllFilesEmitter.pipe(untilDestroyed(this)).subscribe(() => {
                    this.files.forEach(file => this.removeFile(file.name));
                });
            }
            this.setAcceptedMimeTypes();
            this.dropzoneElement = this.dropzone.nativeElement;
            this.inputElement = this.fileInput.nativeElement;

            fromEvent<KeyboardEvent>(this.inputElement, 'focus')
                .pipe(
                    untilDestroyed(this),
                    filter(() => !this.fileLimitReached),
                )
                .subscribe(() => {
                    this.focus = true;
                    this.cdr.detectChanges();
                });

            fromEvent<KeyboardEvent>(this.inputElement, 'blur')
                .pipe(
                    untilDestroyed(this),
                    filter(() => !this.fileLimitReached),
                )
                .subscribe(() => {
                    this.focus = false;
                    this.cdr.detectChanges();
                });

            fromEvent<KeyboardEvent>(this.inputElement, 'change')
                .pipe(
                    untilDestroyed(this),
                    filter(() => !this.fileLimitReached),
                )
                .subscribe(() => {
                    this.focus = false;
                    this.dragover = false;
                    if (this.inputElement.files?.length) {
                        this.addFiles(this.inputElement.files);
                        this.inputElement.value = '';
                    }
                    this.cdr.detectChanges();
                });

            fromEvent<DragEvent>(this.dropzoneElement, 'dragover')
                .pipe(untilDestroyed(this))
                .subscribe((event: DragEvent) => {
                    event.preventDefault();
                    if (!this.fileLimitReached && !this.sizeLimitReached) {
                        this.dragover = true;
                    }
                    this.cdr.detectChanges();
                });

            fromEvent<DragEvent>(this.dropzoneElement, 'dragleave')
                .pipe(untilDestroyed(this))
                .subscribe((event: DragEvent) => {
                    event.preventDefault();
                    this.dragover = false;
                    this.cdr.detectChanges();
                });

            fromEvent<DragEvent>(this.dropzoneElement, 'dragend')
                .pipe(untilDestroyed(this))
                .subscribe((event: DragEvent) => {
                    event.preventDefault();
                    this.dragover = false;
                    this.cdr.detectChanges();
                });

            fromEvent<DragEvent>(this.dropzoneElement, 'drop')
                .pipe(untilDestroyed(this))
                .subscribe((event: DragEvent) => {
                    event.preventDefault();
                    this.dragover = false;
                    this.focus = false;

                    if (event.dataTransfer?.files.length && !this.fileLimitReached) {
                        this.addFiles(event.dataTransfer.files);
                    } else {
                        this.alertModal.show(
                            '',
                            $localize`:@@ADSUIFileErrorLimit: You've reached the maximum amount of files`,
                        );
                    }

                    this.cdr.detectChanges();
                });

            fromEvent<KeyboardEvent>(this.dropzoneElement, 'click')
                .pipe(
                    untilDestroyed(this),
                    filter(() => !this.fileLimitReached && !this.sizeLimitReached),
                )
                .subscribe(() => {
                    this.inputElement.dispatchEvent(new MouseEvent('click'));
                });
        }
    }

    // --- Track index

    trackByIndex(index: number): number {
        return index;
    }
}
