/* eslint-disable no-null/no-null */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable functional/immutable-data */
import { randomId } from '../../utils';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    forwardRef,
    Input,
    ViewChild,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';

@UntilDestroy()
@Component({
    selector: 'ads-form-range-slider',
    templateUrl: './form-range-slider.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // eslint-disable-next-line @angular-eslint/no-forward-ref
            useExisting: forwardRef(() => ADSFormRangeSliderComponent),
            multi: true,
        },
    ],
    styleUrls: ['./form-range-slider.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormRangeSliderComponent implements AfterViewInit {
    @ViewChild('rangeSlider') rangeSlider!: ElementRef;

    @Input() css!: string;
    @Input() min = 0;
    @Input() max = 100;
    @Input() reverse = false;
    @Input() step = 10;

    private wrapperElement!: HTMLDivElement;
    readonly id = `ads-form-range-slider-${randomId()}`;
    className!: string;
    left!: number;
    position!: number;
    width!: number;
    value = 0;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    // ---

    setSliderPosition(): void {
        const range = Math.abs((this.max - this.min) / 100);
        const percentage = !this.reverse
            ? Math.abs((this.min - this.value) / range)
            : Math.abs((this.max - this.value) / range);

        this.left = !this.reverse ? 0 : 100 - percentage;
        this.width = percentage;
        this.position = !this.reverse ? percentage : 100 - percentage;
        this.cdr.detectChanges();
    }

    // ---

    ngAfterViewInit() {
        this.setSliderPosition();
        this.wrapperElement = this.rangeSlider.nativeElement.parentNode;

        fromEvent<InputEvent>(this.rangeSlider.nativeElement, 'focus')
            .pipe(untilDestroyed(this))
            .subscribe(() => {
                this.wrapperElement.classList.add('has-focus');
            });

        fromEvent<InputEvent>(this.rangeSlider.nativeElement, 'blur')
            .pipe(untilDestroyed(this))
            .subscribe(() => {
                this.wrapperElement.classList.remove('has-focus');
            });

        fromEvent<InputEvent>(this.rangeSlider.nativeElement, 'input')
            .pipe(untilDestroyed(this))
            .subscribe((event: any) => {
                this.value = event.target?.value;
                this.setSliderPosition();
            });

        fromEvent<InputEvent>(this.rangeSlider.nativeElement, 'change')
            .pipe(untilDestroyed(this))
            .subscribe(() => {
                this.onChange(this.value);
            });
    }

    // ---

    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: any): void {}

    onTouched(): void {}

    writeValue(value: number | null) {
        if (value !== null) {
            this.value = value;
            this.setSliderPosition();
            this.onChange(this.value);
        }
    }

    registerOnChange(fn: any) {
        this.onChange = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouched = fn;
    }
    /* eslint-enable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
          @typescript-eslint/no-unused-vars */
}
