import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    Output,
    TemplateRef,
    ViewChild,
} from '@angular/core';

@Component({
    selector: 'ads-tab',
    templateUrl: './tab.component.html',
    styleUrls: ['./tab.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSTabComponent {
    @Input() css!: string;
    @Input() id!: string;
    @Input() label!: string;
    @Input() routing!: string;
    @Input() selected = false;

    @Output() readonly clickEmitter = new EventEmitter();

    @ViewChild('tab') templateRef!: TemplateRef<unknown>;

    tabClicked() {
        this.clickEmitter.emit();
    }
}
