/* eslint-disable functional/immutable-data */
import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    Output,
    ViewChild,
} from '@angular/core';

@Component({
    selector: 'ads-edit-in-place',
    templateUrl: './edit-in-place.component.html',
    styleUrls: ['./edit-in-place.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSEditInPlaceComponent {
    @Input() value?: string;
    @Input() placeholder?: string;

    @Output() readonly valueChange = new EventEmitter<string>();

    isEditing = false;

    @ViewChild('inputRef') inputRef?: ElementRef<HTMLInputElement>;
    private copiedValue?: string;

    startEditing() {
        this.isEditing = true;
        this.copiedValue = this.value;
        setTimeout(() => this.inputRef?.nativeElement.focus(), 0);
    }

    cancel() {
        this.value = this.copiedValue;
        this.isEditing = false;
    }

    finishEditing() {
        if (!this.isEditing) return;

        this.isEditing = false;
        if (this.value === this.copiedValue) return;
        this.valueChange.emit(this.value);
    }
}
