/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable functional/immutable-data */
import { DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
} from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { filter, fromEvent } from 'rxjs';
import { ADSKeyEvents, IADSNavigation, IADSNavigationItem } from '../../types';
import { getObjectPath } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-navigation-toolbar',
    templateUrl: './navigation-toolbar.component.html',
    styleUrls: ['./navigation-toolbar.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSNavigationToolbarComponent implements OnChanges, AfterViewInit {
    @Output() readonly clickEmitter: EventEmitter<Event> = new EventEmitter();

    @Input() labelToggle = 'Toggle menu';
    @Input() labelClose = 'Close menu';
    @Input() navigation!: IADSNavigation;

    expandMainMenu = false;
    expandSubMenu = false;
    routerUrl!: string;
    userPath: Array<IADSNavigationItem> = [];
    routerPath: Array<IADSNavigationItem> = [];

    constructor(
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly router: Router,
        private readonly cdr: ChangeDetectorRef,
    ) {
        this.router.events.subscribe((event: any) => {
            if (event instanceof NavigationEnd) {
                this.checkRouterChange(event.url.toLowerCase());
            }
        });
    }

    // --- Menu-bar

    toggleMenu(): void {
        if (!this.expandMainMenu && !this.expandSubMenu) {
            this.expandMainMenu = true;
        } else {
            this.closeMenu();
        }
    }

    openSubMenu(): void {
        if (!this.expandSubMenu) {
            this.expandSubMenu = true;
        }
    }

    closeMenu(): void {
        this.expandSubMenu = false;
        this.expandMainMenu = false;
        this.userPath = this.routerPath;
        this.cdr.detectChanges();
    }

    focusCheck(target: Element, event: KeyboardEvent | undefined = undefined): void {
        const targetDetection =
            !target.closest('.ads-navigation-toolbar') === false ||
            !target.closest('.ads-navigation-toolbar_toggle') === false;

        if (!targetDetection) {
            if (this.expandSubMenu || this.expandMainMenu) {
                this.closeMenu();
            }
        } else {
            if (event && event.key === ADSKeyEvents.KEY_TAB) {
                const url = target.getAttribute('data-url') ?? '';
                const path = getObjectPath(this.navigation, url, 'url');
                const item = path[path.length - 1];
                const parent = this.userPath[this.userPath.length - 1];

                if (parent && parent.children && item && parent.children.indexOf(item) === -1) {
                    this.moveOneLevelUp(event);
                    this.cdr.detectChanges();
                }
            }
        }
    }

    // --- Menu-items

    menuItemSelect(item: IADSNavigationItem, event: Event): void {
        this.userPath = getObjectPath(this.navigation, item.url, 'url');
        this.openSubMenu();

        if (event) {
            if (event.type === 'click') {
                const evt: PointerEvent = event as PointerEvent;
                if (evt.pointerId === -1) {
                    this.menuItemFocus(item);
                }
            }
            if (event.type === 'keyup') {
                const evt: KeyboardEvent = event as KeyboardEvent;
                if (!evt.shiftKey && !item.root) {
                    this.menuItemFocus(item);
                }
            }
        }
    }

    moveOneLevelUp(event: Event): void {
        event.preventDefault();
        event.stopPropagation();
        this.userPath.pop();
    }

    menuItemFocus(item: IADSNavigationItem) {
        const [firstChild] = item?.children ?? [];
        const menuitem: HTMLElement | null = this.document.querySelector(
            `[data-url="${firstChild.url ?? ''}"]`,
        );

        if (menuitem) {
            setTimeout(() => menuitem.focus(), 10);
        }
    }

    menuItemInUserPath(item: IADSNavigationItem): boolean {
        return this.userPath.find(path => path.url === item.url) ? true : false;
    }

    menuItemInRouterPath(item: IADSNavigationItem): boolean {
        return this.routerPath.find(path => path.url === item.url) ? true : false;
    }

    menuItemHasChildren(item: IADSNavigationItem): boolean {
        return item?.children ? item.children.length > 0 : false;
    }

    menuItemHasVisibleSubmenu(item: IADSNavigationItem): boolean {
        const lastItem = this.userPath[this.userPath.length - 1];

        return this.userPath.find(path => path?.url === item.url) &&
            item.url !== (lastItem?.url ?? '')
            ? true
            : false;
    }

    // --- Router

    checkRouterChange(url: string): void {
        if (this.navigation) {
            this.closeMenu();
            this.routerPath = getObjectPath(this.navigation, url, 'url');
            this.userPath = this.routerPath;
            this.cdr.detectChanges();
        }

        this.routerUrl = url;
    }

    // --- click events

    clickEvent(): void {
        this.clickEmitter.emit();
    }

    ngAfterViewInit() {
        fromEvent<MouseEvent>(this.document, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expandMainMenu || this.expandSubMenu),
            )
            .subscribe((event: MouseEvent) => this.focusCheck(event.target as Element));

        fromEvent<KeyboardEvent>(this.document, 'keydown')
            .pipe(
                untilDestroyed(this),
                filter(
                    (event: KeyboardEvent) =>
                        this.expandSubMenu &&
                        (event.key === ADSKeyEvents.KEY_ARROW_DOWN ||
                            event.key === ADSKeyEvents.KEY_ARROW_UP ||
                            event.key === ADSKeyEvents.KEY_ESCAPE),
                ),
            )
            .subscribe((event: KeyboardEvent) => {
                if (
                    event.key === ADSKeyEvents.KEY_ARROW_DOWN ||
                    event.key === ADSKeyEvents.KEY_ARROW_UP
                ) {
                    event.preventDefault();
                }

                if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                    this.closeMenu();
                }
            });

        fromEvent<KeyboardEvent>(this.document, 'keyup')
            .pipe(
                untilDestroyed(this),
                filter((event: KeyboardEvent) => event.key === ADSKeyEvents.KEY_TAB),
            )
            .subscribe((event: KeyboardEvent) => this.focusCheck(event.target as Element, event));

        this.checkRouterChange(this.router.url);
    }

    // --- Misc

    trackByIndex(index: number): number {
        return index;
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { navigation } = changes;

        if (navigation) {
            this.navigation = navigation.currentValue;
            this.navigation.forEach((item: IADSNavigationItem) => {
                item.root = true;
            });
            this.routerPath = getObjectPath(this.navigation, this.routerUrl, 'url');
            this.userPath = this.routerPath;
            this.cdr.detectChanges();
        }
    }
}
