/* eslint-disable no-null/no-null */
/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    Input,
} from '@angular/core';
import { AbstractControl, UntypedFormControl } from '@angular/forms';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { distinctUntilChanged } from 'rxjs';

@UntilDestroy()
@Component({
    selector: 'ads-form-field-validation',
    templateUrl: './form-field-validation.component.html',
    styleUrls: ['./form-field-validation.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormFieldValidationComponent implements AfterViewInit {
    @Input() control!: UntypedFormControl | AbstractControl;
    error = '';

    constructor(private readonly cdr: ChangeDetectorRef) {}

    ngAfterViewInit() {
        if (this.control) {
            this.control.valueChanges
                .pipe(distinctUntilChanged(), untilDestroyed(this))
                .subscribe(() => {
                    const { errors } = this.control;
                    this.error = '';
                    if (errors !== null && !this.control['pristine']) {
                        const [validationError] = Object.keys(errors);
                        this.error = validationError;
                    }

                    this.cdr.detectChanges();
                });
        } else {
            console.warn('Form field validation component: form control name mismatch');
        }
    }
}
