/* eslint-disable functional/immutable-data */
import {
    AfterContentChecked,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ContentChildren,
    ElementRef,
    Input,
    OnChanges,
    QueryList,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { ADSAccordionComponent } from '../../components/accordion/accordion.component';

@UntilDestroy()
@Component({
    selector: 'ads-block-accordions',
    templateUrl: './accordions.block.html',
    styleUrls: ['./accordions.block.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSBlockAccordionsComponent implements OnChanges, AfterContentChecked {
    @Input() css!: string;
    @Input() closeOthers = true;
    @ViewChild('accordions') accordions!: ElementRef;
    @ContentChildren(ADSAccordionComponent) accordionElements!:
        | QueryList<ADSAccordionComponent>
        | undefined;

    private contentChildrenIds: Array<string> = [];

    constructor(private readonly cdr: ChangeDetectorRef) {}

    openCloseAccordionItems(id: string): void {
        if (this.closeOthers && this.accordionElements) {
            this.accordionElements
                ?.filter(element => element.expanded && element.id !== id)
                ?.forEach(element => {
                    element.close();
                });
        }
    }

    updateAccordionElements(): void {
        if (this.accordionElements) {
            this.accordionElements?.forEach(element => {
                element.accordionChange
                    .pipe(untilDestroyed(this))
                    .subscribe((elementId: string) => {
                        this.openCloseAccordionItems(elementId);
                    });
            });
        }
    }

    ngAfterContentChecked(): void {
        if (this.accordionElements) {
            const elementIds: Array<string> = this.accordionElements.map(item => item.id);

            if (elementIds.join('') !== this.contentChildrenIds.join('')) {
                this.updateAccordionElements();

                this.contentChildrenIds = this.accordionElements.map(item => item.id);
            }
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { closeOthers } = changes;

        if (closeOthers) {
            this.closeOthers = closeOthers.currentValue;
            this.cdr.detectChanges();
        }
    }
}
