import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    TemplateRef,
    ViewChild,
} from '@angular/core';

@Component({
    selector: 'ads-step-panel',
    templateUrl: './step-panel.component.html',
    styleUrls: ['./step-panel.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSStepPanelComponent implements OnChanges {
    @Input() selected = false;
    @Input() finished = true;
    @Input() displayButtons = true;
    @Input() previousLabel: string = $localize`:@@uiComponentStepPanelButtonPrevious:Previous step`;
    @Input() nextLabel: string = $localize`:@@uiComponentStepPanelButtonNext:Next step`;

    @Output() readonly stepChangeEmitter: EventEmitter<string> = new EventEmitter<string>();

    @ViewChild('template') readonly template!: TemplateRef<unknown>;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    next(): void {
        this.stepChangeEmitter.emit('next');
    }

    previous(): void {
        this.stepChangeEmitter.emit('previous');
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { finished, selected } = changes;

        if (finished) {
            // eslint-disable-next-line functional/immutable-data
            this.finished = finished.currentValue;
            this.cdr.detectChanges();
        }

        if (selected) {
            // eslint-disable-next-line functional/immutable-data
            this.selected = selected.currentValue;
            this.cdr.detectChanges();
        }
    }
}
