/* eslint-disable functional/immutable-data */
import { ViewportScroller } from '@angular/common';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
} from '@angular/core';

export interface PaginationEvent {
    pageIndex: number;
    pageSize: number;
}

@Component({
    selector: 'ads-paginator',
    templateUrl: './paginator.component.html',
    styleUrls: ['./paginator.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSPaginatorComponent implements OnChanges {
    // Page that is displayed
    @Input() pageIndex = 1;

    // Number of items that are shown per page
    @Input() pageSize = 100;

    // Total number of items that are being paginated
    @Input() length = 0;

    // List of page sizes the user can choose from
    @Input() pageSizeOptions = [25, 50, 100];

    @Input() showPageSelection = false;
    @Input() showPageSizeSelection = false;

    @Input() position: 'left' | 'center' | 'right' = 'left';

    @Output()
    readonly pageChanged: EventEmitter<PaginationEvent> = new EventEmitter<PaginationEvent>();

    totalPages = 0;
    pages: Array<number> = [];

    constructor(
        private readonly cdr: ChangeDetectorRef,
        private readonly scroll: ViewportScroller,
    ) {}

    changePage(page: number) {
        this.pageIndex = page;
        this.pageChanged.emit({ pageIndex: this.pageIndex, pageSize: this.pageSize });
        this.cdr.detectChanges();

        this.scroll.scrollToPosition([0, 0]);
    }

    selectPage(page: string) {
        this.changePage(Number.parseInt(page));
    }

    nextPage() {
        if (this.pageIndex < this.totalPages) {
            this.changePage(this.pageIndex + 1);
        }
    }

    previousPage() {
        if (this.pageIndex > 1) {
            this.changePage(this.pageIndex - 1);
        }
    }

    selectPageSize(pageSize: string) {
        this.pageSize = Number.parseInt(pageSize);
        this.calculatePages();
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { length, pageSize } = changes;

        if (length) {
            this.length = length.currentValue;
        }

        if (pageSize) {
            this.pageSize = pageSize.currentValue;
        }

        if (length || pageSize) {
            this.calculatePages();
        }
    }

    private calculatePages() {
        this.totalPages = Math.ceil(this.length / this.pageSize);
        this.pages = Array.from({ length: this.totalPages }, (_element, index) => index + 1);

        const page = Math.max(Math.min(this.pageIndex, this.totalPages), 1);
        this.changePage(page);
    }

    trackByIndex(index: number): number {
        return index;
    }
}
