import { Inject, Injectable } from '@angular/core';
import { WINDOW } from '@ng-web-apis/common';

interface DimensionsInterface {
    parent: ClientRect;
    child: ClientRect;
}

const enum Position {
    Top = 'place-floating-element-at-top',
    Bottom = 'place-floating-element-at-bottom',
    Left = 'place-floating-element-left',
    Right = 'place-floating-element-right',
    Fallback = 'place-floating-element-fallback',
}

@Injectable({
    providedIn: 'root',
})
export class CollisionDetectionService {
    private dimensions!: DimensionsInterface;

    constructor(@Inject(WINDOW) readonly windowRef: Window) {}

    detect(parent: HTMLElement, child: HTMLElement): void {
        let placeY = Position.Bottom;
        let placeX = Position.Left;

        /* eslint-disable  */
        parent.classList.remove(
            Position.Top,
            Position.Bottom,
            Position.Left,
            Position.Right,
            Position.Fallback,
        );
        child.style.transform = '';
        this.dimensions = {
            parent: parent.getBoundingClientRect(),
            child: child.getBoundingClientRect(),
        };

        if (
            this.dimensions.parent.bottom + this.dimensions.child.height >
            this.windowRef.innerHeight
        ) {
            placeY = Position.Top;
        }

        if (
            this.dimensions.parent.bottom + this.dimensions.child.height <
            this.windowRef.innerHeight
        ) {
            placeY = Position.Bottom;
        }

        if (this.dimensions.parent.left - this.dimensions.child.width > 0) {
            placeX = Position.Left;
        }

        if (
            this.dimensions.parent.right + this.dimensions.child.width <
            this.windowRef.innerWidth
        ) {
            placeX = Position.Right;
        }

        parent.classList.add(placeX);
        parent.classList.add(placeY);
    }
}
