import { ChangeDetectionStrategy, Component, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
    selector: 'ads-form-checkbox',
    templateUrl: './form-checkbox.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // eslint-disable-next-line @angular-eslint/no-forward-ref
            useExisting: forwardRef(() => ADSFormCheckboxComponent),
            multi: true,
        },
    ],
    styleUrls: ['./form-checkbox.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormCheckboxComponent implements ControlValueAccessor {
    @Input() css!: string;
    @Input() checked!: boolean;
    @Input() disabled!: boolean;
    @Input() id!: string;
    @Input() icon!: string;
    @Input() fullWidth!: boolean;
    @Input() hideLabel!: boolean;
    @Input() label = 'Checkbox Label';
    @Input() labelPosition: 'left' | 'right' = 'right';
    @Input() labelColor: 'primary' | 'accent' | 'default' = 'default';
    @Input() size: 'small' | 'regular' = 'regular';
    @Input() type: 'checkbox' | 'switch' = 'checkbox';
    @Input() value!: string;

    private model!: string | boolean | null;

    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: any): void {}

    onTouched(): void {}

    registerOnChange(fn: any): void {
        this.onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this.onTouched = fn;
    }

    writeValue(value: any) {
        // eslint-disable-next-line no-null/no-null
        if (!this.disabled && value !== null) {
            this.model = value;
        }
    }

    onModelChange(event: boolean) {
        if (!this.disabled) {
            this.model = event;
            this.onChange(this.model);
        }
    }
    /* eslint-enable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
}
