/* eslint-disable functional/immutable-data */
import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    Input,
    OnChanges,
    SimpleChanges,
} from '@angular/core';
import { IconsRegistryService } from '../../assets/icons/icons-registry.service';
import {
    IADSDecoratorsColorsBasic,
    IADSDecoratorsColorsFunctional,
    IADSDecoratorsColorsMain,
} from '../../types';
import { IADSDecoratorsSizesIcon } from '../../types/interfaces/decorators.sizes.icon.interface';

@Component({
    selector: 'ads-icon',
    templateUrl: './icon.component.html',
    styleUrls: ['./icon.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSIconComponent implements OnChanges {
    @Input() color:
        | IADSDecoratorsColorsMain
        | IADSDecoratorsColorsFunctional
        | IADSDecoratorsColorsBasic = 'primary';
    @Input() css!: string;
    @Input() label = 'icon';
    @Input() size: IADSDecoratorsSizesIcon = 'regular';
    @Input() type = '';

    constructor(
        private readonly element: ElementRef,
        private readonly iconsRegistry: IconsRegistryService,
    ) {}

    ngOnChanges(changes: SimpleChanges) {
        const { type } = changes;

        if (type) {
            const i: HTMLElement | null = this.element.nativeElement.querySelector('i');
            let svg = this.iconsRegistry.getIcon(type.currentValue);

            if (i && svg) {
                // fix for this iconset
                if (type.currentValue.indexOf('social') > -1) {
                } else if (type.currentValue.indexOf('car_brand') > -1) {
                    svg = svg?.replace('<svg', '<svg viewBox="0 0 200 200"');
                } else {
                    svg = svg?.replace('<svg', '<svg viewBox="0 0 283.5 283.5"');
                }
                // fix for alphabeth icons with a background
                svg = svg.replace('<path fill="#FFF" d="M0 0h283.5v283.5H0z"/>', '');
                i.innerHTML = svg;
            }
        }
    }
}
