/* eslint-disable no-null/no-null */
/* eslint-disable functional/immutable-data */
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
} from '@angular/core';
import { UntilDestroy } from '@ngneat/until-destroy';
import {
    IADSDecoratorsColorsFunctional,
    IADSDecoratorsColorsMain,
    IADSDecoratorsPositionIcon,
    IADSDecoratorsSizesButton,
    IADSDecoratorsTypesButton,
} from '../../types';

@UntilDestroy()
@Component({
    selector: 'ads-button',
    templateUrl: './button.component.html',
    styleUrls: ['./button.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSButtonComponent implements OnChanges {
    @Output() readonly clickEmitter: EventEmitter<Event> = new EventEmitter();
    @Input() color: IADSDecoratorsColorsMain | IADSDecoratorsColorsFunctional | 'destructive' =
        'primary';
    @Input() css!: string;
    @Input() disabled = false;
    @Input() id!: string;
    @Input() icon = '';
    @Input() iconOnly = false;
    @Input() iconPosition: IADSDecoratorsPositionIcon = 'left';
    @Input() label = '';
    @Input() tooltip = '';
    @Input() loading = false;
    @Input() loadingLabel = 'Loading';
    @Input() notification!: string;
    @Input() size: IADSDecoratorsSizesButton = 'regular';
    @Input() tabindex!: number;
    @Input() type: IADSDecoratorsTypesButton = 'button';

    // aria-inputs
    @Input() ariaControls!: string;
    @Input() ariaExpanded!: string;
    @Input() ariaLabelledBy!: string;

    constructor(private readonly cdr: ChangeDetectorRef) {}

    click(event: MouseEvent): void {
        if (!this.disabled && !this.isLoading()) {
            this.clickEmitter.emit(event);
        }
    }

    isLoading(): boolean {
        return this.loading;
    }

    isDisabled(): boolean | null {
        return this.isLoading() || this.disabled ? true : null;
    }

    isIconOnly(): boolean {
        return this.icon !== '' && this.iconOnly;
    }

    displayIcon(): boolean {
        return this.icon !== '' && !this.isLoading();
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { notification } = changes;

        if (notification) {
            this.notification = notification.currentValue;
            this.cdr.detectChanges();
        }
    }
}
