/* eslint-disable functional/immutable-data */
/* eslint-disable @typescript-eslint/no-explicit-any */

export function getObjectPath(obj: any, value: any, key: string = 'id') {
    const path: Array<any> = [];

    const checkObjectKey = (object: any): boolean => {
        if (!object || (typeof object !== 'object' && !Array.isArray(object))) {
            return false;
        } else if (Object.prototype.hasOwnProperty.call(object, key) && object[key] === value) {
            return true;
        } else if (Array.isArray(object)) {
            for (let i = 0; i < object.length; i++) {
                const result: boolean = checkObjectKey(object[i]);
                if (result) {
                    path.push(object[i]);

                    return result;
                }
            }
        } else {
            for (const k in object) {
                const result: boolean = checkObjectKey(object[k]);
                if (result) {
                    return result;
                }
            }
        }

        return false;
    };

    checkObjectKey(obj);

    return path.reverse();
}
