/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    Input,
    OnChanges,
    SimpleChanges,
} from '@angular/core';
import { UntilDestroy } from '@ngneat/until-destroy';
import * as Prism from 'prismjs';

@UntilDestroy()
@Component({
    selector: 'ads-markdown',
    templateUrl: './markdown.component.html',
    styleUrls: ['./markdown.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSMarkdownComponent implements AfterViewInit, OnChanges {
    @Input() code!: string;
    @Input() css = '';
    @Input() language = 'javascript';

    constructor(private readonly element: ElementRef) {}

    createMarkdown() {
        const container: HTMLDivElement = this.element.nativeElement.querySelector('#markdown');
        const code = this.code || container.innerText;
        const grammar = Prism.languages[this.language];
        const html = Prism.highlight(code, grammar, this.language);

        if (container) {
            container.innerHTML = `
                <pre class="language-${this.language} ${this.css}">${html}</pre>`;
        }
    }

    copyCode(): void {
        navigator.clipboard.writeText(this.code);
    }

    ngAfterViewInit() {
        this.createMarkdown();
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { code } = changes;

        if (code) {
            this.createMarkdown();
        }
    }
}
