/* eslint-disable functional/immutable-data */
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    HostBinding,
    Input,
    OnInit,
    Output,
} from '@angular/core';

import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { EMPTY, first, fromEvent, interval, map, merge, startWith, switchMap } from 'rxjs';

export type CarouselDataItem = {
    src: string;
    alt: string;
};

type AnimationDirection = 'left' | 'right';

@UntilDestroy()
@Component({
    selector: 'ads-carousel',
    templateUrl: './carousel.component.html',
    styleUrls: ['./carousel.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSCarouselComponent implements OnInit {
    @Input() items!: Array<CarouselDataItem>;
    @Input() interval = 8000;
    @Input() showControls = true;
    @Input() showIndicators = true;

    @HostBinding('style.--ads-carousel-animation-duration')
    @Input()
    animationDuration = 450;

    @Output() readonly itemClick = new EventEmitter<CarouselDataItem>();

    activeIndex = 0;
    previousActiveIndex?: number;
    direction: AnimationDirection = 'left';

    private isAnimating = false;

    constructor(private readonly cdr: ChangeDetectorRef, private readonly elRef: ElementRef) {}

    ngOnInit(): void {
        if (!this.items) throw new Error('items property is required for the carousel');

        const el = this.elRef.nativeElement;

        const hoverState$ = merge(
            fromEvent(el, 'mouseenter').pipe(map(() => true)),
            fromEvent(el, 'mouseleave').pipe(map(() => false)),
        ).pipe(startWith(false));

        const pausableInterval$ = hoverState$.pipe(
            switchMap(isHovering => (isHovering ? EMPTY : interval(this.interval))),
        );

        pausableInterval$.pipe(untilDestroyed(this)).subscribe(() => this.goNext());
    }

    goNext() {
        const newIndex = (this.activeIndex + 1) % this.items.length;
        this.updateIndex(newIndex, 'left');
    }

    goPrev() {
        const newIndex = (this.activeIndex - 1 + this.items.length) % this.items.length;
        this.updateIndex(newIndex, 'right');
    }

    setActiveIndex(index: number) {
        const newDirection = index > this.activeIndex ? 'left' : 'right';
        this.updateIndex(index, newDirection);
    }

    getAnimationClass(index: number): string {
        const becomesActive = index === this.activeIndex;
        const firstTime = this.previousActiveIndex === undefined;
        const goesAway = index === this.previousActiveIndex;
        const from = this.direction === 'right' ? 'left' : 'right';
        const to = this.direction;

        if (becomesActive && firstTime) return 'appear';
        if (becomesActive) return `${from}-to-center`;
        if (goesAway) return `center-to-${to}`;

        return 'void';
    }

    trackByIndex(index: number) {
        return index;
    }

    private updateIndex(newIndex: number, newDirection: AnimationDirection) {
        if (this.isAnimating) return;

        this.isAnimating = true;
        this.previousActiveIndex = this.activeIndex;
        this.activeIndex = newIndex;
        this.direction = newDirection;
        this.cdr.markForCheck();

        interval(this.animationDuration)
            .pipe(first(), untilDestroyed(this))
            .subscribe(() => (this.isAnimating = false));
    }
}
