/* eslint-disable functional/immutable-data */
import { DOCUMENT } from '@angular/common';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    forwardRef,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ADSKeyEvents, IFormMultiSelectItem } from '../../types';
import { CollisionDetectionService, randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-form-multi-select',
    templateUrl: './form-multi-select.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // eslint-disable-next-line @angular-eslint/no-forward-ref
            useExisting: forwardRef(() => ADSFormMultiSelectComponent),
            multi: true,
        },
    ],
    styleUrls: ['./form-multi-select.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSFormMultiSelectComponent implements OnChanges, ControlValueAccessor {
    @ViewChild('multiselect') multiselect!: ElementRef;
    @Input() allSelector!: boolean;
    @Input() valueAsNumberedResult!: boolean;
    @Input() css!: string;
    @Input() data!: Array<IFormMultiSelectItem>;
    @Input() label = '';
    @Input() disabled = false;
    @Output() readonly optionClicked: EventEmitter<IFormMultiSelectItem> = new EventEmitter();
    readonly id = `al-form-multi-select-${randomId()}`;
    readonly allText = 'All';

    private buttonElement!: HTMLButtonElement;
    private inputElement!: HTMLInputElement;
    private optionsElement!: HTMLUListElement;
    private optionsElementItems!: Array<HTMLLIElement>;
    private checkboxItems!: HTMLCollection;
    private selectedOption = 0;
    private expanded = false;

    value!: Array<string>;
    selectAll = false;
    displayedValue!: Array<string>;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly collisionDetection: CollisionDetectionService,
        private readonly cdr: ChangeDetectorRef,
    ) {}

    buttonClick(): void {
        this.expanded ? this.hide() : this.inputElement.focus();
    }

    // --- show / hide ---

    show(): void {
        this.expanded = true;
        this.inputElement.setSelectionRange(
            this.inputElement.value.length,
            this.inputElement.value.length,
        );
        this.multiselect.nativeElement.classList.add('is-expanded');
        this.collisionDetection.detect(this.multiselect.nativeElement, this.optionsElement);

        this.selectOption();
    }

    private hide(): void {
        this.expanded = false;
        this.inputElement.setAttribute('aria-expanded', 'false');
        this.multiselect.nativeElement.classList.remove('is-expanded');
        this.selectedOption = 0;
    }

    // --- item selection from events ---

    private first(): void {
        this.deselectOption();
        this.selectedOption = 0;
        this.selectOption();
    }

    private last(): void {
        this.deselectOption();
        this.selectedOption = this.optionsElementItems.length - 1;
        this.selectOption();
    }

    private next(): void {
        this.deselectOption();
        this.selectedOption =
            this.selectedOption + 1 > this.optionsElementItems.length - 1
                ? 0
                : this.selectedOption + 1;
        this.selectOption();
    }

    private previous(): void {
        this.deselectOption();
        this.selectedOption =
            this.selectedOption - 1 < 0
                ? this.optionsElementItems.length - 1
                : this.selectedOption - 1;
        this.selectOption();
    }

    private deselectOption(): void {
        const item: HTMLLIElement | null = this.optionsElementItems[this.selectedOption];
        item?.removeAttribute('aria-selected');
        const checkbox: HTMLInputElement | null =
            this.optionsElementItems[this.selectedOption].querySelector('.ads-form-checkbox');
        checkbox?.blur();
    }

    private selectOption(): void {
        const item: HTMLLIElement | null = this.optionsElementItems[this.selectedOption];
        item?.setAttribute('aria-selected', 'true');
        this.inputElement.setAttribute('aria-activedescendant', item.id);
        const checkbox: HTMLInputElement | null =
            this.optionsElementItems[this.selectedOption].querySelector('.ads-form-checkbox');
        checkbox?.focus();
    }

    // --- checkbox changes

    selectAllItems(select: boolean): void {
        this.data.forEach(item => (item.selected = select));
        this.selectedValues();
    }

    selectItem(item: IFormMultiSelectItem): void {
        this.optionClicked.emit(item);
        this.selectedValues();
    }

    selectedValues(): void {
        this.selectAll = this.data.every(item => item.selected);

        const val: Array<string> = [];
        const lab: Array<string> = [];
        this.data.forEach(item => {
            if (item.selected) {
                val.push(item.value);
                lab.push(item.label);
            }
        });

        this.displayedValue =
            !this.selectAll || this.data.length < 2
                ? !this.valueAsNumberedResult
                    ? lab
                    : [`${lab.length} of ${this.data.length}`]
                : [this.allText];
        this.cdr.detectChanges();
        this.onChange(val);
    }

    // ---

    trackByIndex(index: number): number {
        return index;
    }

    // ---

    init(): void {
        this.buttonElement = this.multiselect.nativeElement.querySelector(`.ads-button`);
        this.inputElement = this.multiselect.nativeElement.querySelector(`#${this.id}`);
        this.optionsElement = this.multiselect.nativeElement.querySelector('ul');
        this.optionsElementItems = Array.from(this.optionsElement.querySelectorAll('li')).filter(
            (item: HTMLLIElement) => item.querySelector('.ads-form-checkbox'),
        );
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        this.checkboxItems = this.optionsElement.querySelectorAll('.ads-form-checkbox') as any;

        this.optionsElement.setAttribute('tabindex', '-1');
        this.optionsElement.setAttribute('id', `${this.id}-list`);
        this.optionsElement.setAttribute('role', 'listbox');
        this.optionsElementItems.forEach((item, index) => {
            item.setAttribute('role', 'option');
            item.setAttribute('id', `${this.id}-option-${index}`);
        });

        this.selectedValues();

        if (this.inputElement && this.checkboxItems?.length > 0) {
            fromEvent<KeyboardEvent>((this.inputElement, this.checkboxItems), 'keydown')
                .pipe(
                    untilDestroyed(this),
                    filter(() => this.expanded),
                )
                .subscribe((event: KeyboardEvent) => {
                    if (event.key === ADSKeyEvents.KEY_ARROW_DOWN) {
                        event.preventDefault();
                        this.next();
                    }
                    if (event.key === ADSKeyEvents.KEY_ARROW_UP) {
                        event.preventDefault();
                        this.previous();
                    }
                    if (event.key === ADSKeyEvents.KEY_HOME) {
                        event.preventDefault();
                        this.first();
                    }
                    if (event.key === ADSKeyEvents.KEY_END) {
                        event.preventDefault();
                        this.last();
                    }
                    if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                        if (this.document.activeElement !== this.buttonElement) {
                            event.preventDefault();

                            this.hide();

                            this.inputElement.blur();

                            this.buttonElement.focus();
                        } else {
                            this.buttonElement.blur();
                        }
                    }

                    if (event.key === ADSKeyEvents.KEY_TAB) {
                        if (
                            this.document.activeElement ===
                            this.optionsElementItems[
                                this.optionsElementItems.length - 1
                            ].querySelector('input[type="checkbox"]')
                        ) {
                            this.hide();
                        }
                    }
                });
        }

        fromEvent<MouseEvent>(this.document, 'click')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe((event: MouseEvent) => {
                const { target } = event;
                if (
                    target instanceof HTMLElement &&
                    !this.multiselect.nativeElement.contains(target)
                ) {
                    this.hide();
                }
            });

        fromEvent(this.windowRef, 'resize')
            .pipe(
                untilDestroyed(this),
                filter(() => this.expanded),
            )
            .subscribe(() =>
                this.collisionDetection.detect(this.multiselect.nativeElement, this.optionsElement),
            );
    }

    // --- data changes ---

    ngOnChanges(changes: SimpleChanges): void {
        const { data } = changes;
        if (
            data?.currentValue &&
            JSON.stringify(data.currentValue) !== JSON.stringify(data.previousValue)
        ) {
            setTimeout(() => this.init(), 0);
        }
    }

    /* eslint-disable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
    onChange(value: any): void {}

    onTouched(): void {}

    writeValue(value: Array<string>) {
        this.value = value;
    }

    registerOnChange(fn: any) {
        this.onChange = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouched = fn;
    }
    /* eslint-enable @typescript-eslint/no-empty-function, @typescript-eslint/no-explicit-any, functional/immutable-data,
	   @typescript-eslint/no-unused-vars */
}
