/* eslint-disable functional/immutable-data */
import { DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { filter, fromEvent } from 'rxjs';
import { ADSKeyEvents } from '../../types';
import { randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-side-bar',
    templateUrl: './side-bar.component.html',
    styleUrls: ['./side-bar.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSSideBarComponent implements OnChanges, AfterViewInit {
    @Output() readonly closeEmitter: EventEmitter<boolean> = new EventEmitter();
    @ViewChild('sidebar') sidebar!: ElementRef;

    @Input() labelClose = 'Close sidebar';
    @Input() openerId!: string;
    @Input() title!: string;
    @Input() visible = false;
    @Input() hideBodyScrollBar = false;

    hidden = true;
    css: 'is-visible' | 'is-hidden' = 'is-hidden';
    overflowY = 'auto';
    readonly id = `sidebar-${randomId()}`;

    constructor(
        private readonly cdr: ChangeDetectorRef,
        @Inject(DOCUMENT) private readonly document: Document,
    ) {}

    openSideBar(): void {
        this.hidden = false;
        this.cdr.detectChanges();
        if (this.hideBodyScrollBar) {
            this.overflowY = document.body.style.overflowY || 'auto';
            document.body.style.overflowY = 'hidden';
        }

        setTimeout(() => {
            this.css = 'is-visible';
            this.cdr.detectChanges();
            this.focusOnCloser();
        }, 1);
    }

    closeSideBar(): void {
        this.css = 'is-hidden';
        this.cdr.detectChanges();
        if (this.hideBodyScrollBar) {
            document.body.style.overflowY = this.overflowY;
        }
        setTimeout(() => {
            this.hidden = true;
            this.cdr.detectChanges();
            this.closeEmitter.emit(true);
            this.focusOnOpener();
        }, 300);
    }

    focusOnCloser(): void {
        this.sidebar.nativeElement.querySelector(`#ads-button-${this.id}`)?.focus();
    }

    focusOnOpener(): void {
        if (this.openerId) {
            this.document.getElementById(this.openerId)?.focus();
        }
    }

    ngAfterViewInit(): void {
        // focus trap & escape
        fromEvent<KeyboardEvent>(this.sidebar.nativeElement, 'keydown')
            .pipe(
                untilDestroyed(this),
                filter(() => this.visible),
            )
            .subscribe(event => {
                if (event.key === ADSKeyEvents.KEY_ESCAPE) {
                    this.closeSideBar();
                }
            });

        // mousedown outside
        fromEvent<MouseEvent>(this.sidebar.nativeElement, 'mousedown')
            .pipe(
                untilDestroyed(this),
                filter(() => this.visible),
            )
            .subscribe((event: MouseEvent) => {
                const target = event.target as Element;
                if (document.body.contains(target) && !target.closest('.ads-side-bar')) {
                    this.closeSideBar();
                }
            });
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { visible, title, openerId } = changes;

        if (typeof visible?.currentValue !== 'undefined') {
            visible.currentValue === true ? this.openSideBar() : this.closeSideBar();
        }

        if (title?.currentValue) {
            this.title = title.currentValue;
            this.cdr.detectChanges();
        }

        if (openerId?.currentValue) {
            this.openerId = openerId.currentValue;
        }
    }
}
