/* eslint-disable functional/immutable-data */
import { DOCUMENT } from '@angular/common';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    Input,
    OnDestroy,
    Output,
    ViewChild,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { fromEvent, Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ADSKeyEvents } from '../../types';
import { focusTrap } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-confirmation-box',
    templateUrl: './confirmation-box.component.html',
    styleUrls: ['./confirmation-box.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSConfirmationBoxComponent implements OnDestroy {
    @ViewChild('confirmationBox') readonly confirmationBox!: ElementRef;
    @Input() type: 'confirm' | 'alert' | 'no-actions' = 'confirm';
    @Output() readonly confirmed: EventEmitter<boolean> = new EventEmitter();

    title!: string;
    message!: string;
    visible = false;
    sourceElement?: HTMLElement;
    events: Array<Subscription> = [];

    constructor(
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly cdr: ChangeDetectorRef,
    ) {}

    ngOnDestroy(): void {
        this.unsubscribeAll();
    }

    confirmKeydown(event: KeyboardEvent): void {
        if (event.key === ADSKeyEvents.KEY_ESCAPE && this.visible) {
            this.cancel();
        }
        if (event.key === ADSKeyEvents.KEY_ENTER && this.visible) {
            if (
                document.activeElement ===
                this.confirmationBox.nativeElement.querySelector('#ads-button-ok')
            ) {
                this.ok();
            } else {
                if (
                    document.activeElement ===
                    this.confirmationBox.nativeElement.querySelector('#ads-button-cancel')
                ) {
                    this.cancel();
                }
            }
        }
        if (event.key === ADSKeyEvents.KEY_TAB) {
            focusTrap(event, this.confirmationBox.nativeElement);
        }
    }

    ok(): void {
        this.confirmed.emit(true);
        this.hide();
    }

    cancel(): void {
        this.confirmed.emit(false);
        this.hide();
    }

    show(
        title: string,
        message: string = '',
        sourceElement: HTMLElement | undefined = undefined,
    ): void {
        this.sourceElement = sourceElement;
        this.title = title || this.title;
        this.message = message;
        this.visible = true;
        this.cdr.detectChanges();
        this.confirmationBox.nativeElement.querySelector('#ads-button-ok')?.focus();
        this.bindConfirmationEvents();
    }

    hide(): void {
        this.visible = false;
        this.sourceElement?.focus();
        this.cdr.detectChanges();
        this.unsubscribeAll();
    }

    private bindConfirmationEvents(): void {
        const keydown = fromEvent<KeyboardEvent>(this.document.body, 'keydown')
            .pipe(
                untilDestroyed(this),
                filter(() => this.visible),
            )
            .subscribe(event => this.confirmKeydown(event));

        const wheel = fromEvent<WheelEvent>(this.document.body, 'wheel', { passive: false })
            .pipe(
                untilDestroyed(this),
                filter(() => this.visible),
            )
            .subscribe(event => {
                event.preventDefault();
                event.stopPropagation();

                return false;
            });

        this.events.push(keydown, wheel);
    }

    private unsubscribeAll() {
        this.events.forEach(e => e.unsubscribe());
        this.events = [];
    }
}
