/* eslint-disable functional/immutable-data */
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    Inject,
    Input,
    ViewChild,
} from '@angular/core';
import { WINDOW } from '@ng-web-apis/common';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { debounceTime, fromEvent } from 'rxjs';
import { randomId } from '../../utils';

@UntilDestroy()
@Component({
    selector: 'ads-horizontal-scroller',
    templateUrl: './horizontal-scroller.component.html',
    styleUrls: ['./horizontal-scroller.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ADSHorizontalScrollerComponent implements AfterViewInit {
    @ViewChild('scrollable') scrollable!: ElementRef;

    @Input() css!: string;
    @Input() labelScrollButtonLeft = 'Scroll to the previous item';
    @Input() labelScrollButtonRight = 'Scroll to the next item';

    readonly id = `ads-horizontal-scroller-${randomId()}`;

    scrollParent!: HTMLElement;
    scrollObject!: Record<string, number | boolean>;

    constructor(
        @Inject(WINDOW) private readonly windowRef: Window,
        private readonly cdr: ChangeDetectorRef,
    ) {}

    initScrollable(): void {
        const parent = this.scrollParent;

        this.scrollObject = {
            width: parent.offsetWidth,
            scrollLeft: parent.scrollLeft,
            scrollWidth: parent.scrollWidth,
            leftButton: parent.scrollLeft > 0,
            rightButton: parent.scrollWidth >= parent.offsetWidth + parent.scrollLeft + 10,
        };

        this.cdr.detectChanges();
    }

    updateScrollable(): void {
        const parent = this.scrollParent;

        this.scrollObject['leftButton'] = parent.scrollLeft > 0;
        this.scrollObject['rightButton'] =
            parent.scrollWidth >= parent.offsetWidth + parent.scrollLeft + 10;

        this.cdr.detectChanges();
    }

    scroll(direction: string): void {
        this.scrollable.nativeElement.scrollBy({
            left:
                direction === 'right'
                    ? this.scrollable.nativeElement.offsetWidth
                    : -this.scrollable.nativeElement.offsetWidth,
            behavior: 'smooth',
        });
    }

    ngAfterViewInit(): void {
        this.scrollParent = this.scrollable.nativeElement;
        this.initScrollable();

        fromEvent(this.windowRef, 'resize')
            .pipe(untilDestroyed(this), debounceTime(100))
            .subscribe(() => this.initScrollable());

        fromEvent(this.scrollable.nativeElement, 'scroll')
            .pipe(untilDestroyed(this), debounceTime(50))
            .subscribe(() => this.updateScrollable());
    }
}
